import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../../services/authentication/authentication.service';
import { ShopService } from '../../../services/shop/shop.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { NotificationService } from '../../../services/notification/notification.service';
import { Howl, Howler } from 'howler';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  constructor(
    private autService: AuthenticationService,
    private shopService: ShopService,
    private router: Router,
    private toastr: ToastrService,
    private notificationService: NotificationService
  ) {
    this.notificationService.requestPermission();
    this.notificationService.receiveMessage();
    this.notificationService.currentMessage.subscribe(
      (data: any) => {
        console.log({ ...data, intials: 'AF' });
        this.notifications.unshift({ ...data, intials: 'AF' });
        this.totalNotification = this.totalNotification + 1;
        this.toastr.info(data.message, data.title, {
          positionClass: 'toast-bottom-left',
          closeButton: true,
        });
        // var sound = new Howl({
        //   src: ['../assets/notification/sound1.wav'],
        // });

        // sound.play();
      },
      (error) => {}
    );
  }

  ngOnInit(): void {
    this.getMyShops();
    this.getNotification();
  }

  toggle: boolean = false;
  shops: Array<Object>;
  notifications: any = [];
  totalNotification: any;

  openNotification() {
    this.toggle = !this.toggle;
  }

  getMyShops() {
    this.shopService.shops.subscribe((data: any) => {
      this.shops = data;
      this.shopService.selectedShop = data[0].id;
      this.shopService.setSelectedShopsId(data[0].id);
    });
  }

  setSelectedShop(event) {
    this.shopService.selectedShop = event.target.value;
    this.shopService.setSelectedShopsId(event.target.value);
  }

  toDashboard() {
    this.router.navigate(['merchant/dashboard']);
  }

  toProfile() {
    this.router.navigate(['merchant/profile']);
  }

  getNotification() {
    this.notificationService.getNotification().subscribe((data) => {
      this.notifications = data.data;
      this.notifications.map((item) => {
        item.intials =
          item.senderName.split(' ')[0].charAt(0) + item.senderName.split(' ')[1].charAt(0);
      });
      this.totalNotification = data.meta.total;
    });
  }

  logout() {
    this.autService.logout();
    localStorage.removeItem('user');
    this.router.navigate(['/store/home']);

    // this.router.navigate(['/login']);
  }
}
