import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ShoppingCartService {
  constructor() {}

  private cart = [];

  addProduct(product) {
    if (this.cart.find((it) => it.productlibraryid == product.productlibraryid)) {
      const index = this.cart.findIndex((it) => it.productlibraryid == product.productlibraryid);
      console.log(this.cart[index].quantity);
      this.cart[index].quantity += product.quantity;
      console.log(this.cart[index].quantity);
    } else {
      const p = JSON.stringify(product);
      const p1 = JSON.parse(p);
      this.cart.push(p1);
    }
  }

  addBundle(product) {
    this.cart.push(product);
  }

  getCart() {
    return this.cart;
  }

  trash() {
    this.cart = [];
  }

  deleteItem(index) {
    this.cart.splice(index, 1);
  }
}
