import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ShopService } from '../../../../services/shop/shop.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import {
  SearchCountryField,
  TooltipLabel,
  CountryISO,
  PhoneNumberFormat,
} from 'ngx-intl-tel-input';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private shopService: ShopService
  ) {
    this.shopService.selectedShopId.subscribe((data: any) => {
      if (data) {
        this.getShopDetail(data);
        this.getShopImages(data);
      }
    });
  }

  ngOnInit(): void {}
  Arr = Array;
  shopDetail: any;
  shopImages: any = [];

  getShopDetail(shopId) {
    this.spinner.show();
    this.shopService.getShopById(shopId).subscribe(
      (data) => {
        this.shopDetail = data.body;
        console.log(this.shopDetail);
        this.fillFormFields(this.shopDetail);
        // this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  getShopImages(shopId) {
    this.shopService.getImagesByShopId(shopId).subscribe(
      (data) => {
        this.shopImages = data.body;
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
      }
    );
  }

  setCoverImage(index) {
    this.spinner.show();
    this.shopService.updateImageById(this.shopImages[index].id).subscribe(
      (data) => {
        // this.toastr.success(data.message, 'Success');
        const isNotACoverImage = data.result.find((item) => item.isCoverImage == false).id;
        this.shopImages[
          this.shopImages.findIndex((item) => item.id == isNotACoverImage)
        ].isCoverImage = false;
        this.shopImages[index].isCoverImage = true;
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  deleteImage(index) {
    this.spinner.show();
    this.shopService.deleteImageById(this.shopImages[index].id).subscribe(
      (data) => {
        // this.toastr.success(data.message, 'Success');
        this.shopImages.splice(index, 1);
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  formData = new FormData();
  onFileChange(event) {
    if (event.target.files.length + this.shopImages.length <= 6) {
      for (var i = 0; i < event.target.files.length; i++) {
        var mimeType = event.target.files[i].type;
        if (mimeType.match(/image\/*/) == null) {
          return;
        }
        this.formData.append(`image${i + 1}`, event.target.files[i], `image${i + 1}.png`);
        this.uploadImage();
      }
    } else {
      this.toastr.error('You can only add upto 6 Photos', 'Error');
    }
  }

  uploadImage() {
    this.spinner.show();
    const shopId = this.shopService.selectedShopId.getValue();
    this.formData.append(`shopId`, shopId.toString());
    this.shopService.uploadImages(this.formData).subscribe(
      (data) => {
        this.formData = new FormData();
        data.map((item) => {
          this.shopImages.push(item);
        });
        // this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  TooltipLabel = TooltipLabel;
  CountryISO = CountryISO;
  PhoneNumberFormat = PhoneNumberFormat;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom,
    CountryISO.Canada,
  ];
  phone;

  shop = new FormGroup(
    {
      businessName: new FormControl('', [Validators.required]),
      latitude: new FormControl(62.0321651, [Validators.required]),
      longitude: new FormControl(62.0321651, [Validators.required]),
      businessAddress1: new FormControl('', [Validators.required]),
      businessAddress2: new FormControl('', [Validators.required]),
      businessCity: new FormControl('', [Validators.required]),
      businessState: new FormControl('', [Validators.required]),
      businessCountry: new FormControl('', [Validators.required]),
      businessPostalCode: new FormControl('', [Validators.required]),
      mailingAddress1: new FormControl('', [Validators.required]),
      mailingAddress2: new FormControl('', [Validators.required]),
      mailingCity: new FormControl('', [Validators.required]),
      mailingState: new FormControl('', [Validators.required]),
      mailingCountry: new FormControl('', [Validators.required]),
      mailingPostalCode: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required]),
      phoneNumber: new FormControl('', [Validators.required]),
      isManageByOwner: new FormControl(false, [Validators.required]),
    },
    { updateOn: 'blur' }
  );

  get businessName() {
    return this.shop.get('businessName');
  }
  get businessAddress1() {
    return this.shop.get('businessAddress1');
  }
  get businessAddress2() {
    return this.shop.get('businessAddress2');
  }
  get businessCity() {
    return this.shop.get('businessCity');
  }
  get businessState() {
    return this.shop.get('businessState');
  }
  get businessCountry() {
    return this.shop.get('businessCountry');
  }
  get businessPostalCode() {
    return this.shop.get('businessPostalCode');
  }
  get mailingAddress1() {
    return this.shop.get('mailingAddress1');
  }
  get mailingAddress2() {
    return this.shop.get('mailingAddress2');
  }
  get mailingCity() {
    return this.shop.get('mailingCity');
  }
  get mailingState() {
    return this.shop.get('mailingState');
  }
  get mailingCountry() {
    return this.shop.get('mailingCountry');
  }
  get mailingPostalCode() {
    return this.shop.get('mailingPostalCode');
  }
  get email() {
    return this.shop.get('email');
  }
  get phoneNumber() {
    return this.shop.get('phoneNumber');
  }
  get isManageByOwner() {
    return this.shop.get('isManageByOwner');
  }
  get file() {
    return this.shop.get('file');
  }

  fillFormFields(shopDetail) {
    console.log(shopDetail.isManageByOwner);
    const group: FormGroup = this.shop as FormGroup;
    group.controls.businessName.setValue(shopDetail.businessName || '');
    group.controls.latitude.setValue(shopDetail.latitude || '');
    group.controls.longitude.setValue(shopDetail.longitude || '');
    group.controls.businessAddress1.setValue(shopDetail.businessAddress1 || '');
    group.controls.businessAddress2.setValue(shopDetail.businessAddress2 || '');
    group.controls.businessCity.setValue(shopDetail.businessCity || '');
    group.controls.businessState.setValue(shopDetail.businessState || '');
    group.controls.businessCountry.setValue(shopDetail.businessCountry || '');
    group.controls.businessPostalCode.setValue(shopDetail.businessPostalCode || '');
    group.controls.mailingAddress1.setValue(shopDetail.mailingAddress1 || '');
    group.controls.mailingAddress2.setValue(shopDetail.mailingAddress2 || '');
    group.controls.mailingCity.setValue(shopDetail.mailingCity || '');
    group.controls.mailingState.setValue(shopDetail.mailingState || '');
    group.controls.mailingCountry.setValue(shopDetail.mailingCountry || '');
    group.controls.mailingPostalCode.setValue(shopDetail.mailingPostalCode || '');
    group.controls.email.setValue(shopDetail.email || '');
    group.controls.phoneNumber.setValue(shopDetail.phoneNumber || '');
    group.controls.isManageByOwner.setValue(shopDetail.isManageByOwner || false);
    group.controls.phoneNumber.setErrors(null);
  }

  onSubmit() {
    const group: FormGroup = this.shop as FormGroup;
    console.log(group);
    if (this.shop.valid) {
      group.value.phoneNumber = group.value.phoneNumber.e164Number;
      this.spinner.show();
      this.shopService
        .updateShop(group.value, this.shopService.selectedShopId.getValue())
        .subscribe(
          (data) => {
            console.log(data);
            this.spinner.hide();
            // this.toastr.success(data.message, 'Success');
          },
          (error) => {
            this.toastr.error(error.error.message, 'Error');
            this.spinner.hide();
          }
        );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  formattedaddress = ' ';
  lat = '';
  lng = '';
  fulladdress = ' ';
  mapAddress;
  houseNumber;
  street;
  mapCity = '';
  mapState;
  mapcountry;
  mapPaostalCode;

  options = {
    componentRestrictions: {
      country: ['CA'],
    },
  };
  public AddressChange(address: any) {
    const group: FormGroup = this.shop as FormGroup;
    this.formattedaddress = address.formatted_address;
    console.log(address);

    this.lat = address.geometry.location.lat();
    this.lng = address.geometry.location.lng();
    this.fulladdress = address.formatted_address;
    for (let i = 1; i < address.address_components.length; i++) {
      this.mapAddress = address.address_components[i];
      if (this.mapAddress.long_name != '') {
        if (this.mapAddress.types[0] == 'street_number') {
          this.houseNumber = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'route') {
          this.street = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] === 'locality') {
          this.mapCity = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'administrative_area_level_1') {
          this.mapState = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'country') {
          this.mapcountry = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'postal_code') {
          this.mapPaostalCode = this.mapAddress.long_name + ',';
        }
        let streets = this.street && this.houseNumber ? this.houseNumber + ', ' + this.street : '';
        this.mapAddress = streets ? streets : this.street;
      }
    }
    console.log(this.lat);
    console.log(this.lng);
    console.log(this.street);
    console.log(this.mapCity);
    console.log(this.mapState);
    console.log(this.mapcountry);
    console.log(this.mapPaostalCode);
    group.controls.businessAddress1.setValue(address.name || '');
    group.controls.businessAddress2.setValue(this.street || '');
    group.controls.businessCity.setValue(this.mapCity || '');
    group.controls.businessState.setValue(this.mapState || '');
    group.controls.businessCountry.setValue(this.mapcountry || '');
    group.controls.businessPostalCode.setValue(this.mapPaostalCode || '');
    group.controls.latitude.setValue(this.lat || '');
    group.controls.longitude.setValue(this.lng || '');
  }

  fields = [
    {
      label: 'Business Name',
      placeholder: '',
      for: 'businessName',
      id: 'businessName',
      formControlName: 'businessName',
      control: this.businessName,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Business Address 1',
      placeholder: '',
      for: 'businessAddress1',
      id: 'businessAddress1',
      formControlName: 'businessAddress1',
      control: this.businessAddress1,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Business Address 2',
      placeholder: '',
      for: 'businessAddress2',
      id: 'businessAddress2',
      formControlName: 'businessAddress2',
      control: this.businessAddress2,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Business City',
      placeholder: '',
      for: 'businessCity',
      id: 'businessCity',
      formControlName: 'businessCity',
      control: this.businessCity,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Business State',
      placeholder: '',
      for: 'businessState',
      id: 'businessState',
      formControlName: 'businessState',
      control: this.businessState,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Business Country',
      placeholder: '',
      for: 'businessCountry',
      id: 'businessCountry',
      formControlName: 'businessCountry',
      control: this.businessCountry,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Business Postal Code',
      placeholder: '',
      for: 'businessPostalCode',
      id: 'businessPostalCode',
      formControlName: 'businessPostalCode',
      control: this.businessPostalCode,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Email',
      placeholder: '',
      for: 'email',
      id: 'email',
      formControlName: 'email',
      control: this.email,
      type: 'email',
      size: 'col-lg-8',
    },
    {
      label: 'Phone Number',
      placeholder: '',
      for: 'phoneNumber',
      id: 'phoneNumber',
      formControlName: 'phoneNumber',
      control: this.phoneNumber,
      type: 'ngx-intl-tel-input',
      size: 'col-lg-8',
    },
    {
      label: 'Owner itself managing shop',
      placeholder: '',
      for: 'isManageByOwner',
      id: 'isManageByOwner',
      formControlName: 'isManageByOwner',
      control: this.isManageByOwner,
      type: 'checkbox',
      size: 'col-lg-8',
    },
  ];

  mailingFields = [
    {
      label: 'Mailing Address 1',
      placeholder: '',
      for: 'mailingAddress1',
      id: 'mailingAddress1',
      formControlName: 'mailingAddress1',
      control: this.mailingAddress1,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Mailing Address 2',
      placeholder: '',
      for: 'mailingAddress2',
      id: 'mailingAddress2',
      formControlName: 'mailingAddress2',
      control: this.mailingAddress2,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Mailing City',
      placeholder: '',
      for: 'mailingCity',
      id: 'mailingCity',
      formControlName: 'mailingCity',
      control: this.mailingCity,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Mailing State',
      placeholder: '',
      for: 'mailingState',
      id: 'mailingState',
      formControlName: 'mailingState',
      control: this.mailingState,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Mailing Country',
      placeholder: '',
      for: 'mailingCountry',
      id: 'mailingCountry',
      formControlName: 'mailingCountry',
      control: this.mailingCountry,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Mailing Postal Code',
      placeholder: '',
      for: 'mailingPostalCode',
      id: 'mailingPostalCode',
      formControlName: 'mailingPostalCode',
      control: this.mailingPostalCode,
      type: 'text',
      size: 'col-lg-8',
    },
  ];
}
