import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { DeliveryLocation } from './deliveryLocation';

@Injectable({
  providedIn: 'root',
})
export class ProductCatalogService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  filter = new BehaviorSubject<any>({
    shopId: '',
    categoryId: '',
    ethnicities: [],
    brands: [],
    minPrice: 0,
    maxPrice: 100,
    date: 'recent',
    distance: 10000,
  });

  deliveryLocation = new BehaviorSubject<any>({
    latitude: '0',
    longitude: '0',
    area: '',
  });

  search = new BehaviorSubject<any>({});

  shop = new BehaviorSubject<any>({});

  addProduct(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/product`, payload, {
      headers,
    });
  }

  bulkUpsertProducts(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/bulkUpsert`, payload, {
      headers,
    });
  }

  getProductsByShop(
    shopId,
    pageNum = 1,
    pageSize = 10,
    location: any = {},
    sort: any = {}
  ): Observable<any> {
    // const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    // headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/products/${shopId}?pageNum=${pageNum}&pageSize=${pageSize}${
        location.latitude ? '&latitude=' + location.latitude : ''
      }${location.longitude ? '&longitude=' + location.longitude : ''}${
        sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  getAllProducts(pageSize, pageNum): Observable<any> {
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/products?pageSize=${pageSize}&pageNum=${pageNum}`
    );
  }

  updateProductByProductId(payload, productId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}inventory/products/${productId}`, payload, {
      headers,
    });
  }

  deleteProductById(payload, productId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.request<any>('delete', `${this.baseURL}inventory/product/${productId}`, {
      body: payload,
      headers,
    });
  }

  bulkUpload(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/products`, payload, {
      headers,
    });
  }

  setFilter(newValue): void {
    this.filter.next(newValue);
  }

  setLocation(newValue): void {
    this.deliveryLocation.next(newValue);
  }

  setSearch(newValue): void {
    this.search.next(newValue);
  }

  setShop(newValue): void {
    this.shop.next(newValue);
  }

  getFilteredProducts(
    pageSize,
    pageNum,
    filter: any = {},
    location,
    sort: any = {}
  ): Observable<any> {
    let headers = new HttpHeaders();
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/filter?pageSize=${pageSize}&pageNum=${pageNum}${
        filter.ethnicity ? '&ethnicity=[' + filter.ethnicity.toString() + ']' : ''
      }${filter.category ? '&category=[' + filter.category.toString() + ']' : ''}${
        location.latitude ? '&latitude=' + location.latitude : ''
      }${location.longitude ? '&longitude=' + location.longitude : ''}${
        filter.distance ? '&distance=' + filter.distance : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  getProductsByCategory(
    pageNum,
    pageSize,
    filter,
    location,
    sort: any = {},
    categoryId
  ): Observable<any> {
    let headers = new HttpHeaders();
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/filter/${categoryId}?pageSize=${pageSize}&pageNum=${pageNum}${
        location.latitude ? '&latitude=' + location.latitude : ''
      }${location.longitude ? '&longitude=' + location.longitude : ''}${
        filter.distance ? '&distance=' + filter.distance : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  getSearchedProducts(
    pageSize,
    pageNum,
    filter: any = {},
    location,
    sort: any = {},
    keyword
  ): Observable<any> {
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/search?pageSize=${pageSize}&pageNum=${pageNum}${
        location.latitude ? '&latitude=' + location.latitude : ''
      }${location.longitude ? '&longitude=' + location.longitude : ''}${
        filter.distance ? '&distance=' + filter.distance : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${
        sort.sort ? '&sort=' + sort.sort : ''
      }&keyword=${keyword}`
    );
  }

  getProductList(
    pageSize,
    pageNum,
    filter: any = {},
    keyword,
    location,
    sort: any = {}
  ): Observable<any> {
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/search?pageSize=${pageSize}&pageNum=${pageNum}${
        location.latitude ? '&latitude=' + location.latitude : ''
      }${location.longitude ? '&longitude=' + location.longitude : ''}${
        filter.distance ? '&distance=' + filter.distance : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}${
        keyword ? '&keyword=' + keyword : ''
      }${filter.categoryId ? '&categoryId=' + filter.categoryId : ''}${
        filter.shopId ? '&shopId=' + filter.shopId : ''
      }${filter.ethnicities?.length ? '&ethnicities=[' + filter.ethnicities.toString() + ']' : ''}${
        filter.brands?.length ? '&brands=[' + filter.brands.toString() + ']' : ''
      }${filter.minPrice ? '&minPrice=' + filter.minPrice : ''}${
        filter.maxPrice ? '&maxPrice=' + filter.maxPrice : ''
      }`
    );
  }
}
