import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ProductCatalogService } from '../../../../services/productCatalog/product-catalog.service';
import { ShoppingCartService } from '../../../../services/shoppingCart/shopping-cart.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
})
export class ProductsComponent implements OnInit {
  @Output() open = new EventEmitter<any>();
  constructor(
    private productService: ProductCatalogService,
    private shoppingCart: ShoppingCartService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {
    this.productService.filter.subscribe((data) => {
      console.log(data);
      this.filter = data;
      this.productService.deliveryLocation.subscribe((data) => {
        console.log(data);
        this.location = data;
        this.productService.search.subscribe((data) => {
          console.log(data);
          this.keyword = data.name;
          this.products = [];
          this.config.currentPage = 1;
          this.getAllProducts();
        });
      });
    });
  }

  ngOnInit(): void {}
  filter;
  keyword;
  sort = {
    orderBy: 'product_name',
    sort: 'DESC',
  };
  config = {
    itemsPerPage: 10,
    currentPage: 1,
    totalItems: null,
  };
  location;
  Arr = Array;
  products = [];

  showDetail(index) {
    this.open.emit(this.products[index]);
  }

  getAllProducts() {
    console.log(this.filter,
      this.keyword,)
    this.spinner.show();
    this.productService
      .getProductList(
        this.config.itemsPerPage,
        this.config.currentPage,
        this.filter,
        this.keyword,
        this.location,
        this.sort
      )
      .subscribe(
        (data) => {
          console.log(data);

          this.products.push(...data.body);
          this.products.map((item) => {
            item.image_link = item.image_link.replace('images/', '');
          });
          this.toastr.success(data.message, 'Success');
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
  }
  addToCart(index) {
    if (this.products[index].stock > 0) {
      this.products[index].quantity = 1;
      this.products[index].stock -= 1;
      this.products[index].type = 'single';
      this.shoppingCart.addProduct(this.products[index]);
    } else {
      this.toastr.error('This Product run out of stock', 'Error');
    }
  }
  sortBy(field) {
    this.products = [];
    this.config.currentPage = 1;
    if (this.sort.orderBy != field) {
      this.sort.sort = this.sort.sort;
    } else {
      if (this.sort.sort === 'ASC') {
        this.sort.sort = 'DESC';
      } else {
        this.sort.sort = 'ASC';
      }
    }
    this.sort.orderBy = field;
    console.log(this.sort);
    this.getAllProducts();
  }

  onScroll() {
    console.log(this.config.currentPage * this.config.itemsPerPage, this.products.length);
    if (this.config.currentPage * this.config.itemsPerPage == this.products.length) {
      this.config.currentPage = this.config.currentPage + 1;
      this.getAllProducts();
    }
  }

  onScrollUp() {
    console.log('123 up');
  }
}
