import { Component, OnInit, ViewChild } from '@angular/core';
import { ProductCatalogService } from '../../../services/productCatalog/product-catalog.service';
import { D3PackedBubbleChartService } from './d3-packed-bubble-chart.service';
import { Chart } from 'chart.js';

  //----------------------line wave chart start--------------------
  import {
    ApexAxisChartSeries,
    ApexChart,
    ApexXAxis,
    ApexYAxis,
    ApexGrid,
    ApexDataLabels,
    ApexStroke,
    ApexTitleSubtitle,
    ApexTooltip,
    ApexLegend,
    ApexPlotOptions,
    ApexFill,
    ApexMarkers,
    ApexTheme,
    ApexNonAxisChartSeries,
    ApexResponsive
  } from "ng-apexcharts";


  export type ChartOptions = {
    series: ApexAxisChartSeries | ApexNonAxisChartSeries;
    colors: string[],
    chart: ApexChart;
    xaxis: ApexXAxis;
    yaxis: ApexYAxis | ApexYAxis[],
    title: ApexTitleSubtitle;
    dataLabels: ApexDataLabels,
    stroke: ApexStroke,
    grid: ApexGrid,
    legend?: ApexLegend,
    tooltip?: ApexTooltip,
    plotOptions?: ApexPlotOptions,
    labels?: string[],
    fill: ApexFill,
    markers?: ApexMarkers,
    theme: ApexTheme,
    responsive: ApexResponsive[]
  };
  export type ChartOptions2 = {
    series: ApexAxisChartSeries | ApexNonAxisChartSeries;
    colors: string[],
    chart: ApexChart;
    xaxis: ApexXAxis;
    yaxis: ApexYAxis | ApexYAxis[],
    title: ApexTitleSubtitle;
    dataLabels: ApexDataLabels,
    stroke: ApexStroke,
    grid: ApexGrid,
    legend?: ApexLegend,
    tooltip?: ApexTooltip,
    plotOptions?: ApexPlotOptions,
    labels?: string[],
    fill: ApexFill,
    markers?: ApexMarkers,
    theme: ApexTheme,
    responsive: ApexResponsive[]
  };

  var $primary3 = "#f29700";
  var $primary2 = "#566573";
  var $primary = "#A04000",
  $success = "#40C057",
  $info = "#2F8BE6",
  $warning = "#F77E17",
  $danger = "#F55252",
  $label_color_light = "#E6EAEE";
var themeColors = [$primary, $warning, $success, $danger, $info];
var themeColors2 = [$primary2, $warning, $success, $danger, $info];
var themeColors3 = [$primary3, $warning, $success, $danger, $info];

  //----------------------line wave chart start--------------------

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss'],
  providers: [D3PackedBubbleChartService]
})
export class DashboardComponent implements OnInit {

  //----------------------line wave chart start--------------------
  @ViewChild("apxchart1") chart: ChartOptions;
  @ViewChild("apxchart2") chart2: ChartOptions2;
  @ViewChild("apxchart3") chart3: ChartOptions2;
  public chartOptions: Partial<ChartOptions>;
  public chartOptions2: Partial<ChartOptions>;
  public chartOptions3: Partial<ChartOptions>;
  //----------------------line wave chart end----------------------

  products = [];

  constructor(
    private productCatalogService: ProductCatalogService,
    private d3PackedBubbleChartService: D3PackedBubbleChartService,
  ) { }

  ngOnInit(): void {
    this.getAllProducts();
    this.d3PackedBubbleChartService.renderChart();

  //----------------------line wave chart start--------------------
   this.LineWaveChart1();
   this.LineWaveChart2();
   this.LineWaveChart3();

  //----------------------line wave chart end---------------------

  }

  getAllProducts() {
    this.productCatalogService.getAllProducts(100, 1).subscribe(
      (data) => {
        this.products = data.body;
        console.log(this.products);
      },
      (error) => {
        console.log(error)
      }
    );
  }



  //----------------------line wave chart start--------------------
   LineWaveChart1() {
 
    this.chartOptions = {
      chart: {
        height: 175,
        type: 'area',
        zoom: {
          enabled: false
        }
      },
      colors: themeColors,
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: "smooth"
      },
      series: [{
        name: "Desktops",
        data: [10, 41, 35, 51, 49, 62, 69, 91, 148],
      }],
      title: {
        text: 'Avg. Ratings of Stores',
        align: 'center'
      },
      grid: {
        row: {
          colors: ['transparent'], // takes an array which will be repeated on columns
          opacity: 0.5,
        },
      },
      xaxis: {
        categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
      },
      yaxis: {
        tickAmount: 5,
      }
    }


  }

   LineWaveChart2() {
 
    this.chartOptions2 = {
      chart: {
        height: 175,
        type: 'area',
        zoom: {
          enabled: false
        }
      },
      colors: themeColors2,
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: "smooth"
      },
      series: [{
        name: "Desktops",
        data: [10, 41, 35, 51, 49, 62, 69, 91, 148],
      }],
      title: {
        text: 'Avg. Ratings of Stores',
        align: 'center'
      },
      grid: {
        row: {
          colors: ['transparent'], // takes an array which will be repeated on columns
          opacity: 0.5,
        },
      },
      xaxis: {
        categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
      },
      yaxis: {
        tickAmount: 5,
      }
    }


  }

   LineWaveChart3() {
 
    this.chartOptions3 = {
      chart: {
        height: 280,
        type: 'bar',
        zoom: {
          enabled: false
        }
      },
      colors: themeColors3,
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: "smooth"
      },
      series: [{
        name: 'Inflation',
        data: [2.3, 3.1, 4.0, 10.1, 4.0, 3.6, 3.2, 2.3, 1.4, 0.8, 0.5, 0.2],
      }],
      // title: {
      //   text: 'Avg. Ratings of Stores',
      //   align: 'center'
      // },
      grid: {
        row: {
          colors: ['transparent'], // takes an array which will be repeated on columns
          opacity: 0.5,
        },
      },
      plotOptions: {
        bar: {
            horizontal: false,
            columnWidth: '50%',
            barHeight: '70%',
            distributed: false,
            rangeBarOverlap: true,
            rangeBarGroupRows: false,
            colors: {
                ranges: [{
                    from: 0,
                    to: 0,
                    color: undefined
                }],
                backgroundBarColors: [],
                backgroundBarOpacity: 1,
                backgroundBarRadius: 0,
            },
            dataLabels: {
                position: 'top',
                maxItems: 100,
                hideOverflowingLabels: true,
                // orientation: horizontal
            }
        }
    },
      xaxis: {
        categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep'],
        position: 'top',
        axisBorder: {
          show: false
        },
      },
      yaxis: {
        tickAmount: 5,
      }
    }
    

  }




  public generateData(baseval, count, yrange) {
    var i = 0;
    var series = [];
    while (i < count) {
      var x = Math.floor(Math.random() * (750 - 1 + 1)) + 1;
      var y =
        Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min;
      var z = Math.floor(Math.random() * (75 - 15 + 1)) + 15;

      series.push([x, y, z]);
      baseval += 86400000;
      i++;
    }
    return series;
  }

  //----------------------line wave chart end---------------------

  // ---------progress bar----start------------------

  // ---------progress bar----end--------------------


}
