import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { UserService } from '../../../../../services/user/user.service';
import { TicketService } from '../../../../../services/ticket/ticket.service';
import jwt_decode from 'jwt-decode';

@Component({
  selector: 'app-add-new-ticket',
  templateUrl: './add-new-ticket.component.html',
  styleUrls: ['./add-new-ticket.component.scss'],
})
export class AddNewTicketComponent implements OnInit {
  @Output() close = new EventEmitter<any>();
  @Output() upload = new EventEmitter<any>();

  dataList: Array<any> = [];

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private userService: UserService,
    private ticketService: TicketService
  ) {}

  ngOnInit(): void {
    const token = JSON.parse(localStorage.getItem('user'))?.accessToken;
    this.decoded = jwt_decode(token);
    console.log(this.decoded);
    this.updateReporter();
    this.list();
    this.getAdminUsers();
  }

  tickets = new FormGroup(
    {
      topic: new FormControl('', [Validators.required]),
      description: new FormControl('', [Validators.required]),
      assignedTo: new FormControl('', [Validators.required]),
      reporterId: new FormControl('', [Validators.required]),
      reporterType: new FormControl('', [Validators.required]),
      progress: new FormControl(false, [Validators.required]),
      status: new FormControl('new', [Validators.required]),
      firstName: new FormControl({ value: '' }, [Validators.required]),
      lastName: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required]),
      phoneNumber: new FormControl('', [Validators.required]),
    },
    { updateOn: 'change' }
  );

  get topic() {
    return this.tickets.get('topic');
  }
  get description() {
    return this.tickets.get('description');
  }
  get assignedTo() {
    return this.tickets.get('assignedTo');
  }
  get reporterId() {
    return this.tickets.get('reporterId');
  }
  get reporterType() {
    return this.tickets.get('reporterType');
  }
  get progress() {
    return this.tickets.get('progress');
  }
  get status() {
    return this.tickets.get('status');
  }
  get firstName() {
    return this.tickets.get('firstName');
  }
  get lastName() {
    return this.tickets.get('lastName');
  }
  get email() {
    return this.tickets.get('email');
  }
  get phoneNumber() {
    return this.tickets.get('phoneNumber');
  }

  // onSubmitNewTicket() {
  //   // if (this.fileName) {
  //   // this.upload.emit(this.formData);

  //   // } else {
  //   this.toastr.error('Please Select File First', 'Error');
  //   // }
  // }

  list() {
    this.dataList = [
      { code: 1, name: 'shohel' },
      { code: 2, name: 'rana' },
      { code: 3, name: 'shipon' },
    ];
  }

  decoded;
  adminUsers = [];
  config = {
    itemsPerPage: 1000,
    currentPage: 1,
    type: 'admin',
  };

  ticket = {
    topic: '',
    description: '',
    assignedTo: '',
    reporterId: '',
    reporterType: '',
    progress: false,
    status: 'new',
    firstName: '',
    lastName: '',
    email: '',
    phoneNumber: '',
  };

  getAdminUsers() {
    this.userService.getUsers(this.config).subscribe(
      (data) => {
        this.adminUsers = data.data;
        console.log(this.adminUsers);
      },
      (error) => {}
    );
  }

  updateReporter() {
    const group: FormGroup = this.tickets as FormGroup;
    group.controls.reporterId.setValue(this.decoded.id || '');
    group.controls.reporterType.setValue(this.decoded.type || '');
    group.controls.firstName.setValue(this.decoded.firstName || '');
    group.controls.lastName.setValue(this.decoded.lastName || '');
    group.controls.email.setValue(this.decoded.email || '');
    group.controls.phoneNumber.setValue(this.decoded.phoneNumber || '');
  }

  updateAssignedTo() {
    console.log('updating status');
    const group: FormGroup = this.tickets as FormGroup;
    console.log(group.controls.assignedTo.value);
    if (group.controls.assignedTo.value) {
      group.controls.status.setValue('assigned' || '');
    } else {
      group.controls.status.setValue('new' || '');
    }
  }

  // createTicket() {
  //   console.log('type button');
  //   const group: FormGroup = this.tickets as FormGroup;
  //   console.log(group.value);
  //   // this.spinner.show();
  //   // this.ticketService.createTicket(this.ticket).subscribe(
  //   //   (data) => {
  //   //     this.spinner.hide();
  //   // this.close.emit(false);
  //   //   },
  //   //   (error) => {
  //   //     this.toastr.error(error.error.message, 'Error');
  //   //     this.spinner.hide();
  //   //   }
  //   // );
  // }

  onSubmit() {
    const group: FormGroup = this.tickets as FormGroup;
    if (this.tickets.valid) {
      console.log(group.value);
      this.ticketService.createTicket(group.value).subscribe(
        (data) => {
          this.spinner.hide();
          this.close.emit(false);
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }
}
