import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { CustomerService } from '../../../../services/Admin/customer/customer.service';
import { Router } from '@angular/router';
import { jsPDF } from 'jspdf';

@Component({
  selector: 'app-user-logs',
  templateUrl: './user-logs.component.html',
  styleUrls: ['./user-logs.component.scss'],
})
export class UserLogsComponent implements OnInit {
  customerData: any = [];

  isActive: boolean = false;
  fieldHeader: any = [];
  _index: number = 0;
  _sort: string = 'ASC';

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private _customerService: CustomerService,
    private router: Router,
    private _elr: ElementRef
  ) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.customerData.length,
      sort: 'ASC',
      orderBy: 'firstName',
    };

    this.customerData = this.customerData.slice();
  }

  ngOnInit(): void {
    this.getViewCustomerDetail(this.config);
    this.loadFieldHeader();
  }
  p: number = 1;
  config: any;

  pageChanged(event) {
    this.fieldHeader.forEach((head) => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    var fieldHeader = this.fieldHeader;
    fieldHeader[this._index].isActiveAsc = true;
    this.config.sort = this._sort;
    this.config.orderBy = fieldHeader[this._index].orderBy;
    this.config.currentPage = event;
    this.getViewCustomerDetail(this.config);
  }

  navigateToOrderDetail(id) {
    this.router.navigate([`store/dashboard/order-detail`, id]);
  }

  onSortAsc(index: number, headerName: string) {
    this.fieldHeader.forEach((head) => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    this.fieldHeader[index].isActiveAsc = true;
    this._index = index;

    this._sort = 'ASC';
    this.config.sort = 'ASC';
    this.config.orderBy = this.fieldHeader[index].orderBy;
    this.getViewCustomerDetail(this.config);
    console.log('this.config.sort = ASC');
    console.log(this.config.sort);
  }

  onSortDesc(index: number, headerName: string) {
    this.fieldHeader.forEach((head) => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    this.fieldHeader[index].isActiveDesc = true;
    this._index = index;

    this._sort = 'DESC';
    this.config.sort = 'DESC';
    this.config.orderBy = this.fieldHeader[index].orderBy;
    this.getViewCustomerDetail(this.config);
    console.log('this.config.sort = DESC');
    console.log(this.config.sort);
  }

  loadFieldHeader() {
    this.fieldHeader = [
      { name: 'DATE', orderBy: 'date', isActiveAsc: true, isActiveDesc: false, index: 0 },
      { name: 'TIME', orderBy: 'time', isActiveAsc: false, isActiveDesc: false, index: 1 },
      {
        name: 'IP/MAC ADDRESS',
        orderBy: 'ipmacaddess',
        isActiveAsc: false,
        isActiveDesc: false,
        index: 2,
      },
      { name: 'DETAILS', orderBy: 'details', isActiveAsc: false, isActiveDesc: false, index: 2 },
    ];
  }

  drpField = [5, 10, 25, 50];
  selecteddrpOption: any = this.drpField[0];

  drpChangePage(itemsPerPage) {
    console.log('itemsPerPage');
    this.fieldHeader.forEach((head) => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    var fieldHeader = this.fieldHeader;
    fieldHeader[this._index].isActiveAsc = true;

    this.config.orderBy = fieldHeader[this._index].orderBy;
    this.config.sort = this._sort;
    this.config.itemsPerPage = itemsPerPage;
    this.getViewCustomerDetail(this.config);
    console.log('this.config.sort = ASC');
    console.log(this.config.sort);
  }

  drpAction = ['Active', 'Deactive'];
  selecteddrpAction: any = this.drpAction[0];

  drpChangeAction(id) {
    console.log('this.drpChangeAction ');
    console.log(this.customerData);
    var customerData = this.customerData.filter((x) => x.id == id);
    customerData[0].status = customerData[0].status == 'active' ? 'inactive' : 'active';
    console.log(this.customerData);
  }

  library: any;
  keyword: any;

  selectEvent(item) {
    // // do something with selected item
    // // this.products[index].productLibraryId = item.product_id;
    // // this.products[index].productName = item.product_name;
    // console.log(item)
    // if(this.searchBy === 'Products'){
    //   console.log('Products')
    //   this.productCatalogService.setSearch(item)
    //   this.router.navigate(['store/search-products']);
    // }
    // else if(this.searchBy === 'Shop'){
    //   console.log('Shop')
    //   this.productCatalogService.setShop(item)
    //   this.router.navigate(['store/shop-products']);
    // }
  }

  onChangeSearch(search: string) {
    // // fetch remote data from here
    // // And reassign the 'data' which is binded to 'data' property.
    // console.log(search)
    // // this.sendSearch(search)
    // if(this.searchBy === 'Products'){
    //   console.log('Products')
    //   this.sendProductSearch(search)
    // }
    // else if(this.searchBy === 'Shop'){
    //   console.log('Shop')
    //   this.sendShopSearch(search)
    // }
  }
  onFocused(e) {
    // do something
  }

  // //-------get view customer details-start-----
  getViewCustomerDetail(config: any) {
    this.spinner.show();

    this._customerService.getUsers(config).subscribe(
      (data) => {
        console.log('getViewCustomerDetail');
        console.log(data);
        this.customerData = data.data;
        //to add in array active and inactive start--
        data.data.forEach((item) => {
          // item.actions = ["Active", "Inactive"];
          item.actions = [
            { name: 'Active', value: 'active' },
            { name: 'Inactive', value: 'inactive' },
          ];
        });
        //to add in array active and inactive start--

        console.log('this.customerData ');
        console.log(this.customerData);

        this.config = {
          itemsPerPage: config.itemsPerPage,
          currentPage: config.currentPage,
          totalItems: data.total,
        };
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  // //-------get view customer details-end-------

  //---------------------------------------------
  //---------------------------------------------

  exportColumns: any[];
  exportLst: ExportCustomer[];
  //--------export to pdf start-----------------

  exportPdf() {
    import('jspdf').then((jsPDF) => {
      import('jspdf-autotable').then((x) => {
        const doc = new jsPDF.default(0, 0);

        this.exportLst = [];

        if (this.customerData) {
          this.customerData.forEach((data) => {
            this.exportLst.push({
              FirstName: data.firstName,
              LastName: data.lastName,
              Email: data.email,
              Phone: data.phoneNumber,
              Address: data.address1,
              City: data.city,
              Date: data.createdAt,
            });
          });

          // this.driverService.exportDriversFilteredRecords(this.driverDetails).subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });

          doc.autoTable(this.exportColumns, this.exportLst);
          // });
        } else {
          // this.spinner.show();
          // this.driverService.exportAllDrivers().subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });
          //   this.spinner.hide();
          //   this.exportExcel(this.exportLst);
          // });
        }

        //doc.autoTable(this.exportColumns, this.lstDrivers);
        doc.save('CustomerTable.pdf');
      });
    });
  }
  //--------export to pdf end-----------------

  //----------export to excel----start-----------------------
  exportToExcel() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.customerData) {
      this.customerData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportExcel(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportExcel(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsExcelFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to excel----end-------------------------

  //---------------------------------------------
  //----------export to CSV----start-----------------------
  exportToCSV() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.customerData) {
      this.customerData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportCSV(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportCSV(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsCSVFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsCSVFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.csv';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to CSV----end-------------------------
  //---------------------------------------------
}

export interface ExportCustomer {
  FirstName: string;
  LastName: string;
  Email: string;
  Phone: number;
  Address: string;
  City: number;
  Date: number;
}
