import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class OrderService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.orderService;

  createOrder(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}order`, payload, {
      headers,
    });
  }

  getOrderDetail(config: any): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}order?pageNum=${1}&pageSize=${10}`, {
      headers,
    });
  }

  getOrderByShop(shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}order/${shopId}?pageNum=${1}&pageSize=${10}`, {
      headers,
    });
  }

  getOrders(config: any, deliveryStatus, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}orders?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}${
        sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}${
        deliveryStatus ? '&deliveryStatus=' + deliveryStatus : ''
      }`,
      {
        headers,
      }
    );
  }

  getOrderDetailbyId(orderId: number): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}order-detail/${orderId}`, {
      headers,
    });
  }

  getTopCategorySale(limit): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}states/top-category-sales?limit=${limit}`, {
      headers,
    });
  }

  getTopProductSale(limit): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}states/top-product-sales?limit=${limit}`, {
      headers,
    });
  }

  getLastMonthSales(limit): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}states/last-months-sales?limit=${limit}`, {
      headers,
    });
  }

  getLastDaysSales(shopId, limit): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}states/last-days-sales/${shopId}?limit=${limit}`,
      {
        headers,
      }
    );
  }

  getLastDaysStatus(shopId, limit): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}states/last-days-status/${shopId}?limit=${limit}`,
      {
        headers,
      }
    );
  }
}
