import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class PromotionService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  createProductPromotion(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/promotion/product`, payload, {
      headers,
    });
  }

  createBundlePromotion(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}inventory/promotion/bundle`, payload, {
      headers,
    });
  }

  getAllProductsPromotion(pageSize, pageNum, sort: any = {}): Observable<any> {
    return this.httpClient.get<any>(
      `${this.baseURL}inventory/promotion/product?pageSize=${pageSize}&pageNum=${pageNum}
      ${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`
    );
  }

  getShopsWhoHasPromotion(pageNum, pageSize,  sort: any = {}){
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}inventory/promotion/shops?pageSize=${pageSize}&pageNum=${pageNum}
    ${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`, {
      headers,
    });
  }

  getDistinctPromotionByShop(pageNum, pageSize, shopId, sort: any = {}){
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}inventory/promotion/distinct/${shopId}?pageSize=${pageSize}&pageNum=${pageNum}
    ${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`, {
      headers,
    });
  }
  
  getPromotionItem(name, shopId ){
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}inventory/promotion/items/${shopId}?name=${name}`, {
      headers,
    });
  }
}
