import { Component, OnInit } from '@angular/core';
import { ProductCatalogService } from '../../../../services/productCatalog/product-catalog.service';
import { ShoppingCartService } from '../../../../services/shoppingCart/shopping-cart.service';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';

@Component({
  selector: 'app-all-products',
  templateUrl: './all-products.component.html',
  styleUrls: ['./all-products.component.scss'],
})
export class AllProductsComponent implements OnInit {
  constructor(
    private router: Router,
    private productService: ProductCatalogService,
    private shoppingCart: ShoppingCartService,
    private toastr: ToastrService
  ) {
    this.productService.deliveryLocation.subscribe((location) => {
      this.location = location;
      this.getAllProducts(this.sort);
    });
  }

  ngOnInit(): void {}
  location;
  Arr = Array;
  products: any;
  rate = 5;
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };
  getAllProducts(sort) {
    const obj = {
      distance: 5000,
    };
    this.productService.getFilteredProducts(20, 1, obj, this.location, sort).subscribe(
      (data) => {
        this.products = data.body;
        this.products.map((item) => {
          item.image_link = item.image_link.replace('images/', '');
        });
        console.log(this.products);
      },
      (error) => {
        // this.toastr.error(error.error.message, 'Error');
      }
    );
  }

  sortBy(field) {
    if (this.sort.orderBy != field) {
      this.sort.sort = this.sort.sort;
    } else {
      if (this.sort.sort === 'ASC') {
        this.sort.sort = 'DESC';
      } else {
        this.sort.sort = 'ASC';
      }
    }
    this.sort.orderBy = field;
    console.log(this.sort);
    this.getAllProducts(this.sort);
  }

  addToCart(index) {
    if (this.products[index].stock > 0) {
      this.products[index].quantity = 1;
      this.products[index].stock -= 1;
      this.products[index].type = 'single';
      this.shoppingCart.addProduct(this.products[index]);
    } else {
      this.toastr.error('This Product run out of stock', 'Error');
    }
  }

  left() {
    this.products.unshift(this.products.pop());
  }

  right() {
    this.products.push(this.products.shift());
  }

  navigateToNewProducts() {
    this.router.navigate(['store/new-products']);
  }
}
