import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ShopService } from '../../../../services/shop/shop.service';

@Component({
  selector: 'app-all-branches',
  templateUrl: './all-branches.component.html',
  styleUrls: ['./all-branches.component.scss'],
})
export class AllBranchesComponent implements OnInit {
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private shopService: ShopService
  ) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.shops.length,
    };
  }

  ngOnInit(): void {
    this.getShopsWithManager();
  }

  p: number = 1;
  config: any;
  shops = [];

  pageChanged(event) {
    this.config.currentPage = event;
  }

  getShopsWithManager() {
    this.shopService.getMyShopsWithManager().subscribe(
      (data) => {
        this.shops = data.body;
        console.log(this.shops);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  changeShopStatus(status, index, shopId) {
    this.spinner.show();
    this.shopService.updateShop({ status }, shopId).subscribe(
      (data) => {
        this.shops[index].Shop_status = status;
        // this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
}
