import { Component, OnInit, OnChanges, ViewChild, Input, SimpleChanges } from '@angular/core';

import {
  ChartComponent,
  ApexAxisChartSeries,
  ApexChart,
  ApexXAxis,
  ApexDataLabels,
  ApexTooltip,
  ApexStroke,
} from 'ng-apexcharts';

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  xaxis: ApexXAxis;
  stroke: ApexStroke;
  tooltip: ApexTooltip;
  dataLabels: ApexDataLabels;
};

@Component({
  selector: 'app-area',
  templateUrl: './area.component.html',
  styleUrls: ['./area.component.scss'],
})
export class AreaComponent implements OnInit, OnChanges {
  // @ViewChild('chart') chart: ChartComponent;
  @Input() months: any;
  @Input() series1: any;
  public chartOptions: Partial<ChartOptions>;

  constructor() {
    this.chartOptions = {
      series: [
        {
          name: 'series1',
          data: [0, 10],
        },
        // {
        //   name: 'series2',
        //   data: [11, 32],
        // },
      ],
      chart: {
        type: 'area',
      },
      dataLabels: {
        enabled: false,
      },
      stroke: {
        curve: 'smooth',
      },
      xaxis: {
        type: 'datetime',
        categories: ['2021-06', '2021-07'],
        // categories: ['2018-06-1T00:00:00.000Z', '2018-07-1T01:30:00.000Z'],
      },
      tooltip: {
        x: {
          format: 'MM/yy',
          // format: 'dd/MM/yy HH:mm',
        },
      },
    };
  }
  public generateData(baseval, count, yrange) {
    var i = 0;
    var series = [];
    while (i < count) {
      var x = Math.floor(Math.random() * (750 - 1 + 1)) + 1;
      var y = Math.floor(Math.random() * (yrange.max - yrange.min + 1)) + yrange.min;
      var z = Math.floor(Math.random() * (75 - 15 + 1)) + 15;

      series.push([x, y, z]);
      baseval += 86400000;
      i++;
    }
    return series;
  }
  ngOnInit() {}
  ngOnChanges(changes: SimpleChanges) {
    console.log(changes.series1.currentValue);
    this.chartOptions.series[0].data = JSON.parse(changes.series1.currentValue);
    this.chartOptions.xaxis.categories = JSON.parse(changes.months.currentValue);
  }
}
