import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import {
  SearchCountryField,
  TooltipLabel,
  CountryISO,
  PhoneNumberFormat,
} from 'ngx-intl-tel-input';
import { ShopService } from '../../../../../services/shop/shop.service';
import { ManagerService } from '../../../../../services/manager/manager.service';


@Component({
  selector: 'app-manager-details',
  templateUrl: './manager-details.component.html',
  styleUrls: ['./manager-details.component.scss']
})
export class ManagerDetailsComponent implements OnInit {

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private shopService: ShopService,
    private managerService: ManagerService
  ) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.managers.length,
    };
  }

  ngOnInit(): void {
    this.getAllManagersWithShops();
    this.getMyShops();
  }

  

  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  TooltipLabel = TooltipLabel;
  CountryISO = CountryISO;
  PhoneNumberFormat = PhoneNumberFormat;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom,
    CountryISO.Canada,
  ];
  phone;
  shops: Array<Object>;
  manager = new FormGroup(
    {
      firstName: new FormControl('', [Validators.required]),
      lastName: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
      phoneNumber: new FormControl('', [Validators.required]),
    },
    { updateOn: 'blur' }
  );

  get firstName() {
    return this.manager.get('firstName');
  }
  get lastName() {
    return this.manager.get('lastName');
  }
  get email() {
    return this.manager.get('email');
  }
  get password() {
    return this.manager.get('password');
  }
  get phoneNumber() {
    return this.manager.get('phoneNumber');
  }

  p: number = 1;
  config: any;
  managers = [];

  fields = [
    {
      label: 'Seller ID',
      placeholder: 'LG-1234564783',
      for: 'firstName',
      id: 'firstName',
      formControlName: 'firstName',
      control: this.firstName,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'First Name',
      placeholder: 'First Name',
      for: 'firstName',
      id: 'firstName',
      formControlName: 'firstName',
      control: this.firstName,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Middle Name',
      placeholder: 'Middle Name',
      for: 'firstName',
      id: 'firstName',
      formControlName: 'firstName',
      control: this.firstName,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Last Name',
      placeholder: 'Last Name',
      for: 'lastName',
      id: 'lastName',
      formControlName: 'lastName',
      control: this.lastName,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Email',
      placeholder: 'Email',
      for: 'email',
      id: 'email',
      formControlName: 'email',
      control: this.email,
      type: 'email',
      size: 'col-12',
    },
    // {
    //   label: 'Password',
    //   placeholder: 'Password',
    //   for: 'password',
    //   id: 'password',
    //   formControlName: 'password',
    //   control: this.password,
    //   type: 'password',
    //   size: 'col-12',
    // },
    {
      label: 'Cell Number',
      placeholder: 'Cell Number',
      for: 'phoneNumber',
      id: 'phoneNumber',
      formControlName: 'phoneNumber',
      control: this.phoneNumber,
      type: 'ngx-intl-tel-input',
      size: 'col-12',
    },
  ];

  pageChanged(event) {
    this.config.currentPage = event;
  }

  getMyShops() {
    this.shopService.shops.subscribe((data: any) => {
      this.shops = data;
    });
  }

  getAllManagersWithShops() {
    this.managerService.getManagersWithTheirShop().subscribe(
      (data) => {
        this.managers = data.body;
      },
      (error) => {
        console.log(error);
      }
    );
  }
  onSubmit() {
    const group: FormGroup = this.manager as FormGroup;
    if (this.manager.valid) {
      group.value.phoneNumber = group.value.phoneNumber.e164Number;
      this.spinner.show();
      console.log(group.value);
      this.managerService.createManager(group.value).subscribe(
        (data) => {
          console.log(data);
          const obj = {
            shopId: this.shopService.selectedShopId.getValue(),
            managerId: data.result.id,
          };
          this.managerService.assignManagerToShop(obj).subscribe(
            (data) => {
              this.getAllManagersWithShops();
              // this.toastr.success(data.message, 'Success');
              this.spinner.hide();
            },
            (error) => {
              this.toastr.error(error.error.message, 'Error');
              this.spinner.hide();
            }
          );
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

}
