import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class BankService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  bankDetails = new BehaviorSubject<object>({});

  createBankAccount(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}banking`, payload, {
      headers,
    });
  }

  updateBankAccount(payload, shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}banking/${shopId}`, payload, {
      headers,
    });
  }

  getBankDetailsByShopId(shopId) {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}banking/${shopId}`, {
      headers,
    });
  }

  setBankDetail(newValue): void {
    this.bankDetails.next(newValue);
  }
}
