import { ThrowStmt } from '@angular/compiler';
import {
  Component,
  OnInit,
  AfterViewInit,
  Output,
  EventEmitter,
  HostListener,
  Inject,
  ViewChild,
  ElementRef,
} from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Router } from '@angular/router';
import { ProductLibraryService } from '../../../services/productLibrary/product-library.service';
import { ProductCatalogService } from '../../../services/productCatalog/product-catalog.service';
import { Options } from '@angular-slider/ngx-slider';
@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.scss'],
})
export class FilterComponent implements OnInit, AfterViewInit {
  @Output() close = new EventEmitter<any>();
  @ViewChild('filter') scrollFrame: ElementRef;

  constructor(
    private productLibraryService: ProductLibraryService,
    private productService: ProductCatalogService,
    private router: Router,
    @Inject(DOCUMENT) private document: Document
  ) {
    this.getEthnicities();
    this.getBrands();
    this.prePopulate();
    // this.getCategories(0, 0);

    // this.productService.filter.subscribe((data) => {
    //   console.log(data);
    //   data.ethinicities.map((item) => {
    //     this.ethnicities.find((it) => {
    //       it.ethnicity_id == item;
    //     }).isSelected = true;
    //   });
    // });
  }

  ngAfterViewInit() {
    this.setSticky();
  }

  categories = [];
  ethnicities: any = [
    {
      ethnicity_name: 'All',
      isSelected: false,
    },
  ];
  brands: any = [
    {
      brand_name: 'All',
      isSelected: false,
    },
  ];
  Arr = Array;
  selectedRating = 1;
  ratings = [
    {
      rate: 5,
      isSelected: false,
    },
    {
      rate: 4,
      isSelected: false,
    },
    {
      rate: 3,
      isSelected: false,
    },
    {
      rate: 2,
      isSelected: false,
    },
    {
      rate: 1,
      isSelected: false,
    },
  ];
  selectedPrice = '1000';
  selectedDate = 'recent';
  selectedDistance = '10000';
  selectedEthnicity = [];
  selectedBrands = [];
  selectedCategory = 0;
  minPrice: number = 0;
  maxPrice: number = 100;
  priceOptions: Options = {
    floor: 0,
    ceil: 100,
    getSelectionBarColor: (value: number): string => {
      return 'rgb(242, 151, 0)';
    },
    getPointerColor: (value: number): string => {
      return 'rgb(242, 151, 0)';
    },
  };
  distanceOptions: Options = {
    floor: 0,
    ceil: 10000,
    getSelectionBarColor: (value: number): string => {
      return 'rgb(242, 151, 0)';
    },
    getPointerColor: (value: number): string => {
      return 'rgb(242, 151, 0)';
    },
  };
  ngOnInit(): void {}

  selectEthnicity(index) {
    this.ethnicities[index].isSelected = !this.ethnicities[index].isSelected;
    if (!this.ethnicities[index].ethnicity_id) {
      this.ethnicities.map((item) => {
        item.isSelected = this.ethnicities[index].isSelected;
      });
    }
  }

  selectBrand(index) {
    this.brands[index].isSelected = !this.brands[index].isSelected;
    if (!this.brands[index].brands_id) {
      this.brands.map((item) => {
        item.isSelected = this.brands[index].isSelected;
      });
    }
  }

  selectRating(value) {
    // this.ratings.map((item) => {
    //   item.isSelected = false;
    // });
    // this.ratings[index].isSelected = !this.ratings[index].isSelected;
    this.selectedRating = value;
  }

  apply() {
    this.productLibraryService.setCategories(this.categories);
    this.ethnicities.map((item) => {
      if (item.isSelected && item.ethnicity_id >= 0) {
        this.selectedEthnicity.push(item.ethnicity_id);
      }
    });

    this.brands.map((item) => {
      if (item.isSelected && item.brand_id >= 0) {
        this.selectedBrands.push(item.brand_id);
      }
    });

    const obj = {
      ethnicities: this.selectedEthnicity,
      brands: this.selectedBrands,
      categoryId: this.selectedCategory,
      // price: this.selectedPrice,
      minPrice: this.minPrice,
      maxPrice: this.maxPrice,
      // date: this.selectedDate,
      distance: this.selectedDistance,
      rating: this.selectedRating,
    };
    console.log(obj);
    this.productService.setFilter(obj);
    this.close.emit(false);
    this.router.navigate(['store/filter-products']);
  }

  getCategories(parentCategory = 0, level = 0) {
    this.selectedCategory = parentCategory;
    console.log(this.selectedCategory);
    const queryParams = `?parentCategory=${parentCategory}`;
    this.productLibraryService.getCategories(queryParams).subscribe((data) => {
      this.categories[level] = data.body;
    });
  }

  getEthnicities() {
    this.productLibraryService.ethinicity.subscribe((eth) => {
      console.log(eth);
      this.ethnicities.push(...eth);
      if (eth.length == 0) {
        this.productLibraryService.getEthinicity().subscribe((data) => {
          this.productLibraryService.setEthnicity(data.body);
        });
      }
      const newObj = {
        isSelected: false,
      };
      this.ethnicities = this.ethnicities.flat().map((p) => Object.assign(p, newObj));

      //Re populating filters with old values
      const data = this.productService.filter.getValue();
      data.ethnicities.map((item) => {
        const index = this.ethnicities
          .map(function (e) {
            return e.ethnicity_id;
          })
          .indexOf(item);
        this.ethnicities[index].isSelected = true;
      });
    });
  }
  getBrands() {
    this.productLibraryService.brands.subscribe((data) => {
      this.brands.push(...data);
      console.log(this.brands);
      if (data.length == 0) {
        this.productLibraryService.getBrands().subscribe((data) => {
          this.productLibraryService.setBrands(data.body);
        });
      }
      const newObj = {
        isSelected: false,
      };
      this.brands = this.brands.flat().map((p) => Object.assign(p, newObj));

      //Re populating filters with old values
      const result = this.productService.filter.getValue();
      console.log(result);
      result.brands.map((item) => {
        const index = this.brands
          .map(function (b) {
            return b.brand_id;
          })
          .indexOf(item);
        this.brands[index].isSelected = true;
      });
    });
  }

  prePopulate() {
    const result = this.productService.filter.getValue();
    this.categories = this.productLibraryService.categories.getValue();
    console.log(this.categories);
    if (!this.categories.length) {
      this.getCategories(0, 0);
    }
    this.minPrice = result.minPrice;
    this.maxPrice = result.maxPrice;
    this.selectedDistance = result.distance;
    this.selectedCategory = result.categoryId;
    this.selectedRating = result.rating;
  }

  setSticky() {
    const el: HTMLDivElement = this.scrollFrame.nativeElement;
    if (window.pageYOffset > 120) {
      el.classList.add('sticky');
    } else {
      el.classList.remove('sticky');
    }
  }

  @HostListener('window:scroll', [])
  onWindowScroll() {
    this.setSticky();
  }
}
