import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthenticationService } from '../../services/authentication/authentication.service';
declare var $: any;
import {
  SearchCountryField,
  TooltipLabel,
  CountryISO,
  PhoneNumberFormat,
} from 'ngx-intl-tel-input';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ConfirmPasswordDirective } from '../../directives/confirmPassword/confirm-password.directive';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { RecaptchaErrorParameters } from 'ng-recaptcha';
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss'],
})
export class SignupComponent {
  memberType: string;
  regularExpression = /^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z])(?=.*[A-Z]).{8,15}$/;
  PasswordType: string = 'password';
  confirmPasswordType: string = 'password';

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private cf: ConfirmPasswordDirective,
    private authentication: AuthenticationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {
    this.memberType = this.route.snapshot.paramMap.get('type');
    console.log('member');
    
    console.log(this.memberType);
    
    $(function () {
      $('[data-toggle="popover"]').popover({
        html: true,

        content: function () {
          return $('#popover-content').html();
        },
      });
    });
    $('.popover-dismiss').popover({
      trigger: 'focus',
      content: function () {
        return $('#popover-content').html();
      },
    });
  }

   createButton = true;       // Uncomment this line once siteKey for recaptch has been generated
  //createButton = false;         // Remove this line once siteKey for recaptch has been generated
  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  TooltipLabel = TooltipLabel;
  CountryISO = CountryISO;
  PhoneNumberFormat = PhoneNumberFormat;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom,
    CountryISO.Canada,
    CountryISO.Pakistan
  ];
  isTermAndCondition = false;
  isVerifyOTP = false ;
  phone;

  signUp = new FormGroup(
    {
      firstName: new FormControl('', [Validators.required, this.noWhitespaceValidator]),
      middleName: new FormControl(''),
      lastName: new FormControl('', [Validators.required,  this.noWhitespaceValidator]),
      email: new FormControl('', [Validators.required, Validators.email,  this.noWhitespaceValidator]),
      password: new FormControl('', {
        validators: [Validators.required, Validators.minLength(6), Validators.maxLength(15), this.noWhitespaceValidator],
        updateOn: 'change',
      }),
      confirmPassword: new FormControl('', [Validators.required, this.cf.confirmPassword(), this.noWhitespaceValidator]),
      phoneNumber: new FormControl('', [Validators.required]),
      address1: new FormControl('', [Validators.required]),
      address2: new FormControl('', [Validators.required]),
      city: new FormControl('', [Validators.required]),
      state: new FormControl('', [Validators.required]),
      country: new FormControl('', [Validators.required]),
      postalCode: new FormControl('', [Validators.required]),
      latitude: new FormControl('', [Validators.required]),
      longitude: new FormControl('', [Validators.required]),
      termsAndCondition: new FormControl(false, [Validators.requiredTrue]),
    },
    { updateOn: 'blur' }
  );

  get firstName() {
    return this.signUp.get('firstName');
  }
  get middleName() {
    return this.signUp.get('middleName');
  }
  get lastName() {
    return this.signUp.get('lastName');
  }
  get email() {
    return this.signUp.get('email');
  }
  get password() {
    return this.signUp.get('password');
  }
  get confirmPassword() {
    return this.signUp.get('confirmPassword');
  }
  get phoneNumber() {
    return this.signUp.get('phoneNumber');
  }
  get postalCode() {
    return this.signUp.get('postalCode');
  }
  get address1() {
    return this.signUp.get('address1');
  }
  get address2() {
    return this.signUp.get('address2');
  }
  get city() {
    return this.signUp.get('city');
  }
  get state() {
    return this.signUp.get('state');
  }
  get country() {
    return this.signUp.get('country');
  }
  get termsAndCondition() {
    return this.signUp.get('termsAndCondition');
  }

  changePreferredCountries() {
    this.preferredCountries = [CountryISO.India, CountryISO.Canada];
  }
  onSubmit() {
    
    if (!this.regularExpression.test(this.password.value)) {
      this.toastr.error('Invalid Password', '');
      document.getElementById('passwordHint')?.focus();
      return;
    }
    if (!this.termsAndCondition.value) {
      message = 'Please Accept Terms & Conditions';
      this.toastr.error(message, '');
      return;
    }
    console.log(this.password.value);
    const group: FormGroup = this.signUp as FormGroup;
    console.log(this.signUp);
    if (this.signUp.valid) {
      delete group.value.confirmPassword;
      delete group.value.termsAndCondition;
      group.value.phoneNumber = group.value.phoneNumber.e164Number;
      group.value.type = this.memberType;

      console.log(group.value.middleName);
      !group.value.middleName ? delete group.value.middleName : null;
      console.log(group.value);
      this.spinner.show();
      var message;
      this.authentication.signUp(group.value).subscribe(
        (data) => {
          this.toastr.success(data.message, 'Success');
          this.spinner.hide();
          this.isVerifyOTP = true
        },
        (error) => {
          console.log(error);

          message = error.error.message;
          if (error.error.name == "UniquenessError") {
            message = 'Email is already exists'
          }
          this.toastr.error(message, '');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  loginPage() {
    if (this.memberType == 'merchant') {
      this.router.navigate(['store/home']);
    } else {
      this.router.navigate(['login']);
    }
  }

  formattedaddress = ' ';
  lat = '';
  lng = '';
  fulladdress = ' ';
  mapAddress;
  houseAddress = '';
  houseNumber;
  street;
  mapCity = '';
  mapState;
  mapcountry;
  mapPaostalCode;

  options = {
    componentRestrictions: {
      country: ['CA'],
    },
  };
  public AddressChange(address: any) {
    const group: FormGroup = this.signUp as FormGroup;
    this.formattedaddress = address.formatted_address;
    console.log(address);

    this.lat = address.geometry.location.lat();
    this.lng = address.geometry.location.lng();
    this.houseAddress = address.name;
    this.fulladdress = address.formatted_address;
    for (let i = 1; i < address.address_components.length; i++) {
      this.mapAddress = address.address_components[i];
      if (this.mapAddress.long_name != '') {
        if (this.mapAddress.types[0] == 'street_number') {
          this.houseNumber = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'route') {
          this.street = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] === 'locality') {
          this.mapCity = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'administrative_area_level_1') {
          this.mapState = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'country') {
          this.mapcountry = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'postal_code') {
          this.mapPaostalCode = this.mapAddress.long_name;
        }
        let streets = this.street && this.houseNumber ? this.houseNumber + ', ' + this.street : '';
        this.mapAddress = streets ? streets : this.street;
      }
    }
    console.log(this.lat);
    console.log(this.lng);
    console.log(address.name);
    console.log(this.street);
    console.log(this.mapCity);
    console.log(this.mapState);
    console.log(this.mapcountry);
    console.log(this.mapPaostalCode);
    group.controls.address1.setValue(address.name || '');
    group.controls.address2.setValue(this.street || '');
    group.controls.city.setValue(this.mapCity || '');
    group.controls.state.setValue(this.mapState || '');
    group.controls.country.setValue(this.mapcountry || '');
    group.controls.postalCode.setValue(this.mapPaostalCode || '');
    group.controls.latitude.setValue(this.lat || '');
    group.controls.longitude.setValue(this.lng || '');
  }

  fields = [
    {
      label: 'First Name',
      for: 'firstName',
      id: 'firstName',
      formControlName: 'firstName',
      control: this.firstName,
      type: 'text',
      size: 'col-lg-4',
    },
    {
      label: 'Middle Name',
      for: 'middleName',
      id: 'middleName',
      formControlName: 'middleName',
      control: this.middleName,
      type: 'text',
      size: 'col-lg-4',
    },
    {
      label: 'Last Name',
      for: 'lastName',
      id: 'lastName',
      formControlName: 'lastName',
      control: this.lastName,
      type: 'text',
      size: 'col-lg-4',
    },
    {
      label: 'Email',
      for: 'email',
      id: 'email',
      formControlName: 'email',
      control: this.email,
      type: 'email',
      size: 'col-lg-8',
    },
    {
      label: 'Cell Number',
      for: 'phoneNumber',
      id: 'phoneNumber',
      formControlName: 'phoneNumber',
      control: this.phoneNumber,
      type: 'ngx-intl-tel-input',
      size: 'col-lg-8',
    },
    {
      label: 'Password',
      for: 'password',
      id: 'password',
      formControlName: 'password',
      control: this.password,
      type: this.PasswordType,
      size: 'col-lg-8',
    },
    {
      label: 'Confirm Password',
      for: 'confirmPassword',
      id: 'confirmPassword',
      formControlName: 'confirmPassword',
      control: this.confirmPassword,
      type: this.confirmPasswordType,
      size: 'col-lg-8',
    },
    {
      label: 'Address 1',
      for: 'address1',
      id: 'address1',
      formControlName: 'address1',
      control: this.address1,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'Address 2',
      for: 'address2',
      id: 'address2',
      formControlName: 'address2',
      control: this.address2,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'City',
      for: 'city',
      id: 'city',
      formControlName: 'city',
      control: this.city,
      type: 'text',
      size: 'col-lg-8',
    },
    {
      label: 'State',
      for: 'state',
      id: 'state',
      formControlName: 'state',
      control: this.state,
      type: 'text',
      size: 'col-lg-4',
    },
    {
      label: 'Country',
      for: 'country',
      id: 'country',
      formControlName: 'country',
      control: this.country,
      type: 'text',
      size: 'col-lg-4',
    },
    {
      label: 'Postal Code / Zip Code',
      for: 'postalCode',
      id: 'postalCode',
      formControlName: 'postalCode',
      control: this.postalCode,
      type: 'text',
      size: 'col-lg-8',
    },
    // {
    //   label: 'I agree with the term and condition',
    //   for: 'flexCheckDefault',
    //   id: 'flexCheckDefault',
    //   formControlName: 'termsAndCondition',
    //   control: this.termsAndCondition,
    //   type: 'checkbox',
    //   size: 'col-lg-8',
    // },
  ];

  public resolved(captchaResponse: string): void {
    console.log(`Resolved captcha with response: ${captchaResponse}`);
    this.createButton = false;
  }

  public onError(errorDetails: RecaptchaErrorParameters): void {
    console.log(`reCAPTCHA error encountered; details:`, errorDetails);
  }

  toggleTermsAndConditions() {
    this.isTermAndCondition = !this.isTermAndCondition;
  }

  termsAndConditionAgreed(event) {
    console.log(event);
    const group: FormGroup = this.signUp as FormGroup;
    group.controls.termsAndCondition.setValue(event);
    this.toggleTermsAndConditions();
  }

  public noWhitespaceValidator(control: FormControl) {
    // const isWhitespace = (control.value || '').trim().length === 0
    // console.log(isWhitespace);
    // const isValid = !isWhitespace;
    // return isValid ? null : { 'whitespace': true };
    if((control.value as string).indexOf(' ') >= 0){  
      return { 'whitespace': true }; 
  }  
  
}

  toggleOTPVerification(){
    this.isVerifyOTP = !this.isVerifyOTP;
  }
  clearSearch(){
    var s = document.getElementById('country-search-box');    
    //s.in = '';
    console.log(s);
  }
  showPassword(index:any){
    
    if (this.fields[index].type == 'password') {
      this.fields[index].type = 'text';
    } else {
      this.fields[index].type = 'password';
    }
   
  }
    
  
}
