import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StoreComponent } from './store.component';
import { HomeComponent } from './home/home.component';
import { FilterProductsComponent } from './filter-products/filter-products.component';
import { SearchProductsComponent } from './search-products/search-products.component';
import { ShopComponent } from './shop/shop.component';
import { PromotionComponent } from './promotion/promotion.component';
import { BestSellerComponent } from './best-seller/best-seller.component';
import { NewProductsComponent } from './new-products/new-products.component';
import { ProfileComponent } from './profile/profile.component';
import { AllCategoriesComponent } from './all-categories/all-categories.component';
import { AuthenticationGuard } from '../../guards/authentication/authentication.guard';

const routes: Routes = [
  {
    path: '',
    component: StoreComponent,
    children: [
      {
        path: 'home',
        component: HomeComponent,
        pathMatch: 'full',
      },
      {
        path: 'filter-products',
        component: FilterProductsComponent,
        pathMatch: 'full',
      },
      {
        path: 'search-products',
        component: SearchProductsComponent,
        pathMatch: 'full',
      },
      {
        path: 'shop-products',
        component: ShopComponent,
        pathMatch: 'full',
      },
      {
        path: 'promotion-products',
        component: PromotionComponent,
        pathMatch: 'full',
      },
      {
        path: 'best-seller-products',
        component: BestSellerComponent,
        pathMatch: 'full',
      },
      {
        path: 'new-products',
        component: NewProductsComponent,
        pathMatch: 'full',
      },
      {
        path: 'profile',
        component: ProfileComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-category',
        component: AllCategoriesComponent,
        pathMatch: 'full',
      },
      {
        path: 'dashboard',
        loadChildren: () =>
          import('./dashboard/dashboard.module').then((module) => module.DashboardModule),
        canActivate: [AuthenticationGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class StoreRoutingModule {}
