import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { AuthenticationService } from '../../services/authentication/authentication.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
declare var $: any;
@Component({
  selector: 'app-fogot-password',
  templateUrl: './fogot-password.component.html',
  styleUrls: ['./fogot-password.component.scss'],
})
export class FogotPasswordComponent implements OnInit {
  userType: string;
  constructor(
    private router: Router,
    private authentication: AuthenticationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {
   this.userType = localStorage.getItem('userTypeForResetPassword');
   console.log(this.userType);
   
  }

  ngOnInit(): void {}

  forgotPassword = new FormGroup(
    {
      email: new FormControl('', [Validators.required, Validators.email]),
    },
    { updateOn: 'blur' }
  );

  get email() {
    return this.forgotPassword.get('email');
  }

  onSubmit() {
    const group: FormGroup = this.forgotPassword as FormGroup;

    console.log(group);
    if (this.forgotPassword.valid) {
      this.spinner.show();
      this.authentication.forgotPassword(group.value).subscribe(
        (data) => {
          this.toastr.success(data.message, 'Success');
          this.spinner.hide();
          $('#setNewPasswordModal').modal('show');
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  signupPage() {
    this.router.navigate(['signup']);
  }

  loginPage() {
    if (this.userType == 'merchant') {
      this.router.navigate(['store/home']);
    } else {
      this.router.navigate(['login']);
    }
  }
}
