import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { io } from 'socket.io-client';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class SocketService {
  socket;
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.scoketURL;

  suggestions = new BehaviorSubject<Array<object>>([{}]);
  setupSocketConnection() {
    this.socket = io(this.baseURL);
    console.log(this.socket);
  }

  sendMessage(keyword) {
    this.socket.emit('search', keyword);
  }

  getSuggestion(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('suggestion', function (msg) {
        observer.next(msg);
      });
    });
  }
  sendShopMessage(keyword) {
    this.socket.emit('searchShop', keyword);
  }

  sendUserMessage(keyword) {
    this.socket.emit('searchUser', keyword);
  }

  closeConnection() {
    this.socket.close();
  }
}
