import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class ShopService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  shops = new BehaviorSubject<Array<object>>([{}]);
  selectedShop: number;
  selectedShopId = new BehaviorSubject<Number>(null);

  createShop(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}shop`, payload, {
      headers,
    });
  }

  getShops(pageSize, pageNum): Observable<any> {
    return this.httpClient.get<any>(`${this.baseURL}shops?pageSize=${pageSize}&pageNum=${pageNum}`);
  }

  getShopById(shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}shop/${shopId}`, {
      headers,
    });
  }

  getMyShops(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}shop`, {
      headers,
    });
  }

  getMyShopsWithManager(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}shops/manager`, {
      headers,
    });
  }

  updateShop(payload, shopId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}shop/${shopId}`, payload, {
      headers,
    });
  }

  uploadImages(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}shop/image`, payload, {
      headers,
    });
  }

  getImagesByShopId(shopId): Observable<any> {
    // const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    // headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}shop/images/${shopId}`, {
      headers,
    });
  }

  updateImageById(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(
      `${this.baseURL}shop/image/${id}`,
      {},
      {
        headers,
      }
    );
  }

  deleteImageById(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.delete<any>(`${this.baseURL}shop/image/${id}`, {
      headers,
    });
  }

  setShops(newValue): void {
    this.shops.next(newValue);
  }

  setSelectedShopsId(newValue): void {
    this.selectedShopId.next(newValue);
  }
}
