import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-quick-setup',
  templateUrl: './quick-setup.component.html',
  styleUrls: ['./quick-setup.component.scss'],
})
export class QuickSetupComponent implements OnInit {
  constructor() {}

  section: number = 1;

  ngOnInit(): void {}

  continue() {
    if (this.section < 3) {
      this.section++;
    }
  }
}
