import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductLibraryService } from '../../../services/productLibrary/product-library.service';
import { ProductCatalogService } from '../../../services/productCatalog/product-catalog.service';
import { ShoppingCartService } from '../../../services/shoppingCart/shopping-cart.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-all-categories',
  templateUrl: './all-categories.component.html',
  styleUrls: ['./all-categories.component.scss'],
})
export class AllCategoriesComponent implements OnInit {
  constructor(
    private productLibraryService: ProductLibraryService,
    private productService: ProductCatalogService,
    private shoppingCart: ShoppingCartService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.productService.deliveryLocation.subscribe((location) => {
      this.location = location;
    });
    const routeParams = this.router.getCurrentNavigation().extras.state.categoryId;
    const categoryId = Number(this.router.getCurrentNavigation().extras.state.categoryId);
    console.log(categoryId);
    this.getCategories(categoryId);
  }

  ngOnInit(): void {}
  categories = [];
  breadCrumb = [];

  getCategories(parentCategory = 0) {
    console.log('category', parentCategory);
    this.categoryId = parentCategory;
    this.products = [];
    this.config['currentPage'] = 1;
    this.config['itemsPerPage'] = 10;

    this.getAllProducts();
    const queryParams = `?parentCategory=${parentCategory}`;
    this.productLibraryService.getCategories(queryParams).subscribe((data) => {
      this.categories = data.body;
      console.log(this.categories);
      console.log(this.categories.length);
      if (!this.categories.length) {
        console.log('getting produt');
      }
    });
  }

  addCategoryToBreadCrumb(category) {
    this.breadCrumb.push(category);
  }

  removeCategoryFromBreadCrumb(index) {
    this.breadCrumb.splice(index);
  }

  location;
  categoryId;
  Arr = Array;
  products = [];
  rate = 5;
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };
  config = {
    itemsPerPage: 10,
    currentPage: 1,
    totalItems: null,
  };
  getAllProducts() {
    const obj = {
      distance: 5000,
    };
    this.productService
      .getProductsByCategory(
        this.config.currentPage,
        this.config.itemsPerPage,
        obj,
        this.location,
        this.sort,
        this.categoryId
      )
      .subscribe(
        (data) => {
          this.config.totalItems = data.total;
          this.products.push(...data.body);
          this.products.map((item) => {
            item.image_link = item.image_link.replace('images/', '');
          });
          console.log(this.products);
        },
        (error) => {
          // this.toastr.error(error.error.message, 'Error');
        }
      );
  }

  addToCart(index) {
    if (this.products[index].stock > 0) {
      this.products[index].quantity = 1;
      this.products[index].stock -= 1;
      this.products[index].type = 'single';
      this.shoppingCart.addProduct(this.products[index]);
    } else {
      this.toastr.error('This Product run out of stock', 'Error');
    }
  }

  sortBy(field) {
    if (this.sort.orderBy != field) {
      this.sort.sort = this.sort.sort;
    } else {
      if (this.sort.sort === 'ASC') {
        this.sort.sort = 'DESC';
      } else {
        this.sort.sort = 'ASC';
      }
    }
    this.sort.orderBy = field;
    console.log(this.sort);
    this.products = [];
    this.config['currentPage'] = 1;
    this.config['itemsPerPage'] = 10;
    this.getAllProducts();
  }

  onScroll() {
    console.log(this.config.currentPage * this.config.itemsPerPage, this.products.length);
    if (this.config.currentPage * this.config.itemsPerPage == this.products.length) {
      this.config.currentPage = this.config.currentPage + 1;
      this.getAllProducts();
    }
  }
}
