import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { BehaviorSubject, Observable } from 'rxjs';


@Injectable({
  providedIn: 'root'
})
export class StoreService {

  constructor(private httpClient: HttpClient) { }
  baseURL: any = environment.orderService;
  baseAdminURL: any = environment.baseAdminURL;

  //https://localgrocerys-uat.nn.r.appspot.com/users?pageNum=1&pageSize=10&orderBy=firstName&sort=ASC
  getViewCustomerDetail(config: any): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    console.log("log");
    console.log(`${this.baseAdminURL}users?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}&orderBy=${config.orderBy}&sort=${config.sort}`);
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseAdminURL}users?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}&orderBy=${config.orderBy}&sort=${config.sort}`, {
      headers,
    });
  }


}

