import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class CustomerService {
  constructor(private httpClient: HttpClient) { }
  baseURL: any = environment.orderService;
  baseAdminURL: any = environment.baseAdminURL;
  notificationService: any = environment.notificationService;
  users: any = environment.users;

  //https://localgrocerys-uat.nn.r.appspot.com/users?pageNum=1&pageSize=10&orderBy=firstName&sort=ASC
  getUsers(config: any, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseAdminURL}users?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}${sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}${config.userIds ? '&userIds=[' + config.userIds.toString() + ']' : ''
      }`,
      {
        headers,
      }
    );
  }

  getUserById(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    console.log(id);
    return this.httpClient.get<any>(`${this.baseAdminURL}user/${id}`, {
      headers,
    });
  }

  changeCustomerStatus(model: any): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseAdminURL}user`, model, {
      headers,
    });
  }

  updateCustomer(model: any): Observable<any> {
    console.log("updateCustomer service");
    console.log((`${this.baseAdminURL}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseAdminURL}user`, model, {
      headers,
    });
  }

  getCustomerLogs(customer_Id: number, config: any, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.notificationService}notification/logging/${customer_Id}?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}`,
      {
        headers,
      }
    );

    // return this.httpClient.get<any>(
    //   `${this.notificationService}notification/logging/${customer_Id}?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}${
    //     sort.orderBy ? '&orderBy=' + sort.orderBy : ''
    //   }${sort.sort ? '&sort=' + sort.sort : ''}${
    //     config.userIds ? '&userIds=[' + config.userIds.toString() + ']' : ''
    //   }`,
    //   {
    //     headers,
    //   }
    // );

  }
  //notification/logging/3?pageNum=1&pageSize=10

  getUserRoles(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.users}user/roles?pageNum=${1}&pageSize=${1000}`,
      {
        headers,
      }
    );
  }



}
