import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../services/authentication/authentication.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  constructor(
    private router: Router,
    private authentication: AuthenticationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {}

  email;
  password;
  userType;
  ngOnInit(): void {
    this.userType = localStorage.getItem('userTypeForResetPassword');

  }

  signupPage() {
    this.router.navigate(['signup']);
  }
  forgotPasswordPage() {
    this.router.navigate(['forgot-password']);
  }

  login() {
    const payload = {
      email: this.email,
      password: this.password,
      type: this.userType ? this.userType : 'admin',
    };
    this.spinner.show();
    this.authentication.login(payload).subscribe(
      (data) => {
        console.log(data);
        this.toastr.success(data.message, 'Success');
        this.authentication.setLoginStatus(true);
        localStorage.setItem('user', JSON.stringify(data));
        // const redirectUrl = this.authentication.redirectUrl;
        // this.router.navigate([redirectUrl]);
        this.router.navigate(['admin/dashboard']);
        this.spinner.hide();
      },
      (error) => {
        console.log(error);
        this.toastr.error(JSON.parse(error.error.message).message, 'Error');
        this.spinner.hide();
      }
    );
  }
}
