import { Component, OnInit } from '@angular/core';
import { ProductCatalogService } from '../../../../services/productCatalog/product-catalog.service';
import { ShopService } from '../../../../services/shop/shop.service';

@Component({
  selector: 'app-shop-slider',
  templateUrl: './shop-slider.component.html',
  styleUrls: ['./shop-slider.component.scss'],
})
export class ShopSliderComponent implements OnInit {
  constructor(private productService: ProductCatalogService, private shopService: ShopService) {
    this.productService.shop.subscribe((data) => {
      this.shop = data;
      this.getShopImages();
    });
  }

  ngOnInit(): void {}
  shop;
  shopImages = [];
  getShopImages() {
    this.shopService.getImagesByShopId(this.shop.id).subscribe(
      (data) => {
        this.shopImages = data.body;
        console.log(this.shopImages);
      },
      (error) => {}
    );
  }
}
