import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ShopService } from '../../../../../services/shop/shop.service';
import { BankService } from '../../../../../services/bank/bank.service';

@Component({
  selector: 'app-banking-details',
  templateUrl: './banking-details.component.html',
  styleUrls: ['./banking-details.component.scss'],
})
export class BankingDetailsComponent implements OnInit {
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private shopService: ShopService,
    private bankService: BankService
  ) {
    this.bankService.bankDetails.subscribe((data) => {
      this.bankDetail = data;
      console.log(this.bankDetail);
    });
    this.shopService.selectedShopId.subscribe((data: any) => {
      if (data) {
        this.getBankDetails();
      }
    });
  }

  ngOnInit(): void {
    this.getBankDetails();
  }

  bankDetail: any = {};
  account = new FormGroup(
    {
      bankName: new FormControl('', [Validators.required]),
      financialInstitutionNumber: new FormControl('', [Validators.required]),
      transitNo: new FormControl('', [Validators.required]),
      accountNo: new FormControl('', [Validators.required]),
      eTransfer: new FormControl('', [Validators.required, Validators.email]),
    },
    { updateOn: 'blur' }
  );

  get bankName() {
    return this.account.get('bankName');
  }
  get financialInstitutionNumber() {
    return this.account.get('financialInstitutionNumber');
  }
  get transitNo() {
    return this.account.get('transitNo');
  }
  get accountNo() {
    return this.account.get('accountNo');
  }
  get eTransfer() {
    return this.account.get('eTransfer');
  }

  fields = [
    {
      label: 'Financial Institution',
      placeholder: '3-Digit-financial-inst-number',
      for: 'financialInstitutionNumber',
      id: 'financialInstitutionNumber',
      formControlName: 'financialInstitutionNumber',
      control: this.financialInstitutionNumber,
      type: 'number',
      size: 'col-12',
      minLength: 3,
      maxLength: 3,
    },
    {
      label: 'Bank Transit Number',
      placeholder: '5-Digit Transit Number',
      for: 'transitNo',
      id: 'transitNo',
      formControlName: 'transitNo',
      control: this.transitNo,
      type: 'number',
      size: 'col-12',
      minLength: 5,
      maxLength: 5,
    },
    {
      label: 'Bank Account Number',
      placeholder: 'Account Number',
      for: 'accountNo',
      id: 'accountNo',
      formControlName: 'accountNo',
      control: this.accountNo,
      type: 'text',
      size: 'col-12',
    },
    {
      label: 'Branch Name',
      placeholder: 'Branch Name',
      for: 'bankName',
      id: 'bankName',
      formControlName: 'bankName',
      control: this.bankName,
      type: 'text',
      size: 'col-12',
    },
    // {
    //   label: 'Bank Name',
    //   placeholder: 'Bank Name',
    //   for: 'bankName',
    //   id: 'bankName',
    //   formControlName: 'bankName',
    //   control: this.bankName,
    //   type: 'text',
    //   size: 'col-12',
    // },
    // {
    //   label: 'E Transfer',
    //   placeholder: 'Email Address for e-Transfer',
    //   for: 'eTransfer',
    //   id: 'eTransfer',
    //   formControlName: 'eTransfer',
    //   control: this.eTransfer,
    //   type: 'email',
    //   size: 'col-12',
    // },
  ];

  getBankDetails() {
    this.bankService.getBankDetailsByShopId(this.shopService.selectedShop).subscribe(
      (data) => {
        this.bankService.setBankDetail(data.body);
      },
      (error) => {
        this.bankService.setBankDetail({});
      }
    );
  }
  onSubmit() {
    const group: FormGroup = this.account as FormGroup;
    if (this.account.valid) {
      this.spinner.show();

      const payload = {
        shopId: this.shopService.selectedShop,
        ...group.value,
      };
      console.log(payload);
      this.bankService.createBankAccount(payload).subscribe(
        (data) => {
          this.getBankDetails();
          // this.toastr.success(data.message, 'Success');
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  update() {
    const group: FormGroup = this.account as FormGroup;
    if (this.account.valid) {
      this.spinner.show();

      const payload = {
        ...group.value,
      };
      console.log(payload);
      this.bankService
        .updateBankAccount(payload, this.shopService.selectedShopId.getValue())
        .subscribe(
          (data) => {
            this.getBankDetails();
            // this.toastr.success(data.message, 'Success');
            this.spinner.hide();
          },
          (error) => {
            this.toastr.error(error.error.message, 'Error');
            this.spinner.hide();
          }
        );
    } else {
      this.validateAllFormFields(group);
    }
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  maxLengthCheck(object) {
    if (object.target.value.length > object.target.maxLength) {
      object.target.value = object.target.value.slice(0, object.target.maxLength);
      var event = new Event('input');
      object.target.dispatchEvent(event);
    }
  }
}
