import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MerchantComponent } from './merchant.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { GrossSaleComponent } from './dashboard/gross-sale/gross-sale.component';
import { TotalProductsComponent } from './dashboard/total-products/total-products.component';
import { TotalOrdersComponent } from './dashboard/total-orders/total-orders.component';
import { OutOfStockComponent } from './dashboard/out-of-stock/out-of-stock.component';
import { QuickSetupComponent } from './quick-setup/quick-setup.component';
import { ProfileComponent } from './profile/profile.component';
import { StoreComponent } from './store/store.component';
import { TopCategorySalesComponent } from './dashboard/top-category-sales/top-category-sales.component';
import { TopProductSalesComponent } from './dashboard/top-product-sales/top-product-sales.component';
import { LastMonthsSalesComponent } from './dashboard/last-months-sales/last-months-sales.component';

const routes: Routes = [
  {
    path: '',
    component: MerchantComponent,
    children: [
      {
        path: 'dashboard',
        component: DashboardComponent,
        pathMatch: 'full',
      },
      {
        path: 'gross-sale',
        component: GrossSaleComponent,
        pathMatch: 'full',
      },
      {
        path: 'total-products',
        component: TotalProductsComponent,
        pathMatch: 'full',
      },
      {
        path: 'total-orders',
        component: TotalOrdersComponent,
        pathMatch: 'full',
      },
      {
        path: 'out-of-stock',
        component: OutOfStockComponent,
        pathMatch: 'full',
      },
      {
        path: 'quick-setup',
        component: QuickSetupComponent,
        pathMatch: 'full',
      },
      {
        path: 'top-category-sales',
        component: TopCategorySalesComponent,
        pathMatch: 'full',
      },
      {
        path: 'top-products-sales',
        component: TopProductSalesComponent,
        pathMatch: 'full',
      },
      {
        path: 'last-months-sales',
        component: LastMonthsSalesComponent,
        pathMatch: 'full',
      },
      {
        path: 'profile',
        component: ProfileComponent,
        pathMatch: 'full',
      },
      {
        path: 'store',
        loadChildren: () => import('./store/store.module').then((module) => module.StoreModule),
      },
      {
        path: 'branch',
        loadChildren: () => import('./branch/branch.module').then((module) => module.BranchModule),
      },
      {
        path: 'product',
        loadChildren: () =>
          import('./products/products.module').then((module) => module.ProductsModule),
      },
      {
        path: 'offer',
        loadChildren: () => import('./offer/offer.module').then((module) => module.OfferModule),
      },
      {
        path: 'order',
        loadChildren: () => import('./order/order.module').then((module) => module.OrderModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class MerchantRoutingModule {}
