import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class TicketService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.ticketURL;

  createTicket(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}ticket/ticket`, payload, {
      headers,
    });
  }

  getAllTickets(pageNum, pageSize, filter, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);

    return this.httpClient.get<any>(
      `${this.baseURL}ticket/ticket?pageSize=${pageSize}&pageNum=${pageNum}${
        filter.search ? '&search=' + filter.search : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  getById(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}ticket/ticket/${id}`, {
      headers,
    });
  }

  getByAssignee(pageNum, pageSize, filter, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}ticket/ticket/assignee?pageSize=${pageSize}&pageNum=${pageNum}${
        filter.search ? '&search=' + filter.search : ''
      }${sort.orderBy ? '&orderBy=' + sort.orderBy : ''}${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  updateTicket(ticketId, payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}ticket/ticket/${ticketId}`, payload, {
      headers,
    });
  }

  addComment(payload, ticketId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseURL}ticket/comment/${ticketId}`, payload, {
      headers,
    });
  }

  getCommetByTicketId(id): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}ticket/comment/${id}`, {
      headers,
    });
  }
}
