import { Component, OnInit, OnChanges, ViewChild, Input, SimpleChanges } from '@angular/core';
import { ChartComponent } from 'ng-apexcharts';

import { ApexNonAxisChartSeries, ApexResponsive, ApexChart } from 'ng-apexcharts';

export type ChartOptions = {
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  responsive: ApexResponsive[];
  labels: any;
};

@Component({
  selector: 'app-pie',
  templateUrl: './pie.component.html',
  styleUrls: ['./pie.component.scss'],
})
export class PieComponent implements OnInit, OnChanges {
  @ViewChild('chart') chart: ChartComponent;
  @Input() series: any;
  @Input() labels: any;
  public chartOptions: Partial<ChartOptions>;

  constructor() {
    this.chartOptions = {
      series: [10, 55, 13, 43, 22],
      chart: {
        type: 'pie',
      },
      labels: ['Product A', 'Product B', 'Product C', 'Product D', 'Product E'],
      responsive: [
        {
          breakpoint: 480,
          options: {
            chart: {
              width: 200,
            },
            legend: {
              position: 'bottom',
            },
          },
        },
      ],
    };
  }
  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges) {
    this.chartOptions.series = JSON.parse(changes.series.currentValue);
    this.chartOptions.labels = JSON.parse(changes.labels.currentValue);
  }
}
