import { Component, OnInit } from '@angular/core';
import { NotificationService } from '../../../../services/notification/notification.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-contect-us',
  templateUrl: './contect-us.component.html',
  styleUrls: ['./contect-us.component.scss'],
})
export class ContectUsComponent implements OnInit {
  constructor(
    private notificationService: NotificationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {}

  firstName = '';
  lastName = '';
  email = '';
  phoneNumber = '';
  topic = 'feedback';
  orderId: '';
  detail: '';
  contact: '';
  fullName: '';
  issue: '';

  sendSupportEmail() {
    console.log(this.fullName);
    if ((this.contact, this.fullName, this.issue, this.detail, this.orderId)) {
      const payload = {
        from: 'info@localgrocerys.com',
        to: 'arslan_fareed05@outlook.com',
        subject: 'Requesting Support on an Order',
        type: 'email',
        template: 'Customer-Support',
        message: {
          fullName: this.fullName,
          contact: this.contact,
          orderId: this.orderId,
          issue: this.issue,
          detail: this.detail,
        },
      };
      this.spinner.show();
      this.notificationService.sentSupportEmail(payload).subscribe(
        (data) => {
          this.toastr.success('Thanks for reaching us', 'Success');
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.toastr.error('Please fill all the fields', 'Error');
    }
  }
}
