import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductCatalogService } from '../../../../services/productCatalog/product-catalog.service';
import { ShopService } from '../../../../services/shop/shop.service';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-shop-sliders',
  templateUrl: './shop-slider.component.html',
  styleUrls: ['./shop-slider.component.scss'],
})
export class ShopsSliderComponent implements OnInit {
  constructor(
    private router: Router,
    private toastr: ToastrService,
    private shopService: ShopService,
    private productCatalogService: ProductCatalogService
  ) {}

  ngOnInit(): void {
    this.getShops();
  }

  shops;

  getShops() {
    this.shopService.getShops(10, 1).subscribe(
      (data) => {
        console.log(data.body);
        this.shops = data.body.data;
      },
      (error) => {
        // this.toastr.error(error.error.message, 'Error');
      }
    );
  }

  navigateToShop(index) {
    const obj = {
      id: this.shops[index].Shop_id,
      name: this.shops[index].Shop_businessName,
    };
    this.productCatalogService.setShop(obj);
    this.router.navigate(['store/shop-products']);
  }

  left() {
    this.shops.unshift(this.shops.pop());
  }

  right() {
    this.shops.push(this.shops.shift());
  }
}
