import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class UsersService {

  constructor(private httpClient: HttpClient) { }
  baseURL: any = environment.orderService;
  baseAdminURL: any = environment.baseAdminURL;
  notificationService: any = environment.notificationService;
  users: any = environment.users;

  //https://localgrocerys-uat.nn.r.appspot.com/users?pageNum=1&pageSize=10&orderBy=firstName&sort=ASC

  createUser(model: any): Observable<any> {
    console.log("createUser service");
    console.log(model);
    console.log((`${this.baseAdminURL}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.baseAdminURL}user`, model, {
      headers,
    });
  }

  createUserCardInformation(model: any): Observable<any> {
    console.log("createUserCardInformation service");
    console.log(model);
    console.log((`${this.baseAdminURL}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.users}user/bank`, model, {
      headers,
    });
  }


  getUserRoles(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.users}user/roles?pageNum=${1}&pageSize=${1000}`,
      {
        headers,
      }
    );
  }

  getBankInfoByuserId(userId): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    console.log(userId);
    return this.httpClient.get<any>(`${this.users}user/bank/${userId}`, {
      headers,
    });
  }

  updateUser(model: any): Observable<any> {
    console.log("updateUser service");
    console.log((`${this.baseAdminURL}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseAdminURL}user`, model, {
      headers,
    });
  }

  updateUserCardInformation(model: any, bankId): Observable<any> {
    console.log("updateUserCardInformation service");
    console.log(model);
    console.log((`${this.baseAdminURL}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.users}user/bank/${bankId}`, model, {
      headers,
    });
  }

  createRoles(model: any): Observable<any> {
    console.log("createRoles service");
    console.log(model);
    console.log((`${this.users}user`));
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.post<any>(`${this.users}user/role`, model, {
      headers,
    });
  }


  getRoles(config: any, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.users}user/roles?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}`,
      {
        headers,
      }
    );
  }

  getPermissionByRoleId(config: any, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.users}user/permissions?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}`,
      {
        headers,
      }
    );
  }

}

