import { Component, OnInit } from '@angular/core';
import { ShopService } from '../../../../services/shop/shop.service';
import { TimingService } from '../../../../services/timing/timing.service';
import { ITiming } from './ITiming';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
@Component({
  selector: 'app-timing',
  templateUrl: './timing.component.html',
  styleUrls: ['./timing.component.scss'],
})
export class TimingComponent implements OnInit {
  myArr = [];
  constructor(
    private timingService: TimingService,
    private shopService: ShopService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {
    this.shopService.selectedShopId.subscribe((data: any) => {
      if (data) {
        this.shopId = data;
        this.getTimingList(data);
      }
    });
  }
  timeList: ITiming[];
  shopId;
  Arr = Array;
  ngOnInit(): void {}

  getTimingList(shopId) {
    this.timingService.getTimings(shopId).subscribe(
      (res) => {
        console.log(res);
        this.myArr = [];
        this.timeList = [];
        this.timeList = res.body;
        let items: any = JSON.stringify(this.timeList);

        items = JSON.parse(items);
        let myobj = this.convertIntObj(items);
        console.log(myobj);
        for (const property in myobj) {
          this.myArr.push(myobj[property]);
        }
        this.myArr.pop();
        console.log(this.myArr);
      },
      (err: any) => console.log(err)
    );
  }

  convertIntObj(obj) {
    const res = {};
    for (const key in obj) {
      res[key] = [];
      for (const prop in obj[key]) {
        const parsed = parseInt(obj[key][prop], 10);
        res[key][prop] = isNaN(parsed) ? obj[key][prop] : parsed;
      }
    }
    return res;
  }

  updateTime(dayFlag, hour, status) {
    let day = '';
    if (dayFlag == 0) {
      day = 'Sunday';
    } else if (dayFlag == 1) {
      day = 'Monday';
    } else if (dayFlag == 2) {
      day = 'Tuesday';
    } else if (dayFlag == 3) {
      day = 'Wednesday';
    } else if (dayFlag == 4) {
      day = 'Thursday';
    } else if (dayFlag == 5) {
      day = 'Friday';
    } else if (dayFlag == 6) {
      day = 'Saturday';
    }
    status = !status;
    console.log(day, hour, status);
    this.spinner.show();
    this.timingService.updateTimings({ day, hour, status }, this.shopId).subscribe(
      () => {
        console.log(this.myArr[hour - 1][dayFlag]);
        this.myArr[hour - 1][dayFlag] = status;
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
}
