  pageChanged(event) {

    this.fieldHeader.forEach(head => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    var fieldHeader = this.fieldHeader;
    fieldHeader[this._index].isActiveAsc = true;
    this.config.sort = this._sort;
    this.config.orderBy = fieldHeader[this._index].orderBy;
    this.config.currentPage = event;
    this.getViewCustomerDetail(this.config);
  }


  navigateToOrderDetail(id) {
    this.router.navigate([`store/dashboard/order-detail`, id]);
  }

  onSortAsc(index: number, headerName: string) {

    this.fieldHeader.forEach(head => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    this.fieldHeader[index].isActiveAsc = true;
    this._index = index;

    this._sort = "ASC";
    this.config.sort = "ASC";
    this.config.orderBy = this.fieldHeader[index].orderBy;
    this.getViewCustomerDetail(this.config);
    console.log("this.config.sort = ASC");
    console.log(this.config.sort);


  }

  onSortDesc(index: number, headerName: string) {

    this.fieldHeader.forEach(head => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    this.fieldHeader[index].isActiveDesc = true;
    this._index = index;

    this._sort = "DESC";
    this.config.sort = "DESC";
    this.config.orderBy = this.fieldHeader[index].orderBy;
    this.getViewCustomerDetail(this.config);
    console.log("this.config.sort = DESC");
    console.log(this.config.sort);

  }


  loadFieldHeader() {
    this.fieldHeader = [
      { name: "FIRST NAME", orderBy: "firstName", isActiveAsc: true, isActiveDesc: false, index: 0 },
      { name: "LAST NAME", orderBy: "lastName", isActiveAsc: false, isActiveDesc: false, index: 1 },
      { name: "EMAIL", orderBy: "email", isActiveAsc: false, isActiveDesc: false, index: 2 },
      { name: "PHONE", orderBy: "phoneNumber", isActiveAsc: false, isActiveDesc: false, index: 3 },
      { name: "ADDRESS", orderBy: "address1", isActiveAsc: false, isActiveDesc: false, index: 4 },
      { name: "CITY", orderBy: "city", isActiveAsc: false, isActiveDesc: false, index: 5 },
      { name: "DATE", orderBy: "createdAt", isActiveAsc: false, isActiveDesc: false, index: 6 },
    ]
  };


  drpField = [5, 10, 25, 50];
  selecteddrpOption: any = this.drpField[0];

  drpChangePage(itemsPerPage) {

    console.log("itemsPerPage");
    this.fieldHeader.forEach(head => {
      head.isActiveAsc = false;
      head.isActiveDesc = false;
    });
    var fieldHeader = this.fieldHeader;
    fieldHeader[this._index].isActiveAsc = true;

    this.config.orderBy = fieldHeader[this._index].orderBy;
    this.config.sort = this._sort;
    this.config.itemsPerPage = itemsPerPage;
    this.getViewCustomerDetail(this.config);
    console.log("this.config.sort = ASC");
    console.log(this.config.sort);

  }

  drpAction = ["Active", "Deactive"];
