import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductLibraryService } from '../../../../services/productLibrary/product-library.service';

@Component({
  selector: 'app-category-slider',
  templateUrl: './category-slider.component.html',
  styleUrls: ['./category-slider.component.scss'],
})
export class CategorySliderComponent implements OnInit {
  constructor(private productLibraryService: ProductLibraryService, private router: Router) {
    this.getFeaturedCategories();
  }

  ngOnInit(): void {}

  categories = [];

  left() {
    this.categories.unshift(this.categories.pop());
  }

  right() {
    this.categories.push(this.categories.shift());
  }

  getFeaturedCategories() {
    const queryParams = '?isFeatured=true';
    this.productLibraryService.getCategories(queryParams).subscribe((data) => {
      this.categories = data.body;
      console.log(data);
    });
  }

  navigateToCategory(id) {
    console.log(id);
    this.router.navigate([`store/all-category`], { state: { categoryId: id } });
  }
}
