import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { UsersService } from '../../../../services/Admin/user/users.service';

@Component({
  selector: 'app-create-roles',
  templateUrl: './create-roles.component.html',
  styleUrls: ['./create-roles.component.scss']
})
export class CreateRolesComponent implements OnInit {

  // isActive: boolean = true;
  // valueChange = false;
  // account: any;
  // library: any;
  // keyword: any;
  // selectEvent: any;
  // onChangeSearch: any;
  // onFocused: any;
  // dataList: Array<any> = [];

  itemAssignRights: any = [];
  selectedAssignRights: any;
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private _usersService: UsersService,
  ) { }

  ngOnInit(): void {
    // this.list();
    this.getRigetAssign();
  }

  rolesForm = new FormGroup(
    {
      name: new FormControl('', [Validators.required]),
      assignRights: new FormControl('', [Validators.required]),
    },
    { updateOn: 'blur' }
  );


  onSubmitRoles() {
    const group: FormGroup = this.rolesForm as FormGroup;
    if (this.rolesForm.valid) {
      this.spinner.show();
      this._usersService.createRoles(group.value).subscribe(
        (data) => {
          this.toastr.success(data.message, 'Success');
          this.fieldReset();
          this.spinner.hide();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  fieldReset() {
    this.rolesForm.reset();
  }

  get name() {
    return this.rolesForm.get('name');
  }
  get assignRights() {
    return this.rolesForm.get('assignRights');
  }

  //--111
  //------fields start------------------
  roleFields = [
    {
      label: 'Role Name',
      for: 'name',
      id: 'name',
      formControlName: 'name',
      control: this.name,
      type: 'text',
      size: 'col-md-6',
      placeholder: 'Role Name',
    },
    {
      label: 'Assign Rights',
      for: 'assignRights',
      id: 'assignRights',
      formControlName: 'assignRights',
      control: this.assignRights,
      type: 'text',
      size: 'col-md-6',
      placeholder: 'Select',
    },
  ];
  //------fields end--------------------

  isDisabled() {
    if (this.rolesForm.valid) {
      return false;
    } else {
      return true;
    }
  }

  getRigetAssign() {
    this.itemAssignRights = [
      { id: 1, name: 'update password' },
      { id: 2, name: 'create password' },
      { id: 3, name: 'add role' },
      { id: 4, name: 'update role' },
      { id: 5, name: 'change customer status' },
    ];
    // _itemRoles.push({ id: item.id, name: item.name });
  }


  // onValueChange(value: boolean) {
  //   this.valueChange = value;
  // }

  // list() {
  //   this.dataList = [
  //     { code: 1, name: "shohel" },
  //     { code: 2, name: "rana" },
  //     { code: 3, name: "shipon" }
  //   ]
  // }

  // exportPdf() { }
  // exportToExcel() { }
  // exportToCSV() { }

}

