import { Injectable } from '@angular/core';
import { AngularFireMessaging } from '@angular/fire/messaging';
import { BehaviorSubject } from 'rxjs';
import { UserService } from '../user/user.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
@Injectable({
  providedIn: 'root',
})
export class NotificationService {
  currentMessage = new BehaviorSubject({});
  constructor(
    private httpClient: HttpClient,
    private angularFireMessaging: AngularFireMessaging,
    private userService: UserService
  ) {
    this.angularFireMessaging.messages.subscribe((_messaging: any) => {
      _messaging.onMessage = _messaging.onMessage.bind(_messaging);
      _messaging.onTokenRefresh = _messaging.onTokenRefresh.bind(_messaging);
    });
  }

  baseURL: any = environment.notificationService;

  requestPermission() {
    this.angularFireMessaging.requestToken.subscribe(
      (token) => {
        const payload = {
          webToken: token,
        };
        this.userService.updateUserDetail(payload).subscribe(
          (data) => {
            console.log('Updated Successfully');
          },
          (error) => {
            console.log('Unable to update');
          }
        );
        console.log(token);
      },
      (err) => {
        console.error('Unable to get permission to notify.', err);
      }
    );
  }

  receiveMessage() {
    this.angularFireMessaging.messages.subscribe((payload: any) => {
      console.log(JSON.parse(payload.notification.body));
      this.currentMessage.next(JSON.parse(payload.notification.body));
    });
  }

  getNotification() {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}notification`, {
      headers,
    });
  }

  sentSupportEmail(payload){
    return this.httpClient.post<any>(`${this.baseURL}email`, payload );
  }
}
