import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of, Subscription } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { delay } from 'rxjs/operators';
import { Router } from '@angular/router';
@Injectable({
  providedIn: 'root',
})
export class AuthenticationService {
  constructor(private router: Router, private httpClient: HttpClient) {
    const user = JSON.parse(localStorage.getItem('user'));
    if (user) {
      this.setLoginStatus(true);
    }
  }
  baseURL: any = environment.baseURL;
  tokenSubscription = new Subscription();

  isLoggedIn = new BehaviorSubject<boolean>(false);
  redirectUrl: string = '/merchant/dashboard';

  login(payload): Observable<any> {
    return this.httpClient.post<any>(`${this.baseURL}login`, payload);
  }

  logout(): void {
    this.isLoggedIn.next(false);
  }

  getLoginStatus(): Observable<boolean> {
    return this.isLoggedIn.asObservable();
  }

  setLoginStatus(newValue): void {
    this.isLoggedIn.next(newValue);
  }

  signUp(payload): Observable<any> {
    console.log(payload);
    return this.httpClient.post<any>(`${this.baseURL}signup/`, payload);
  }

  verifyPhoneNumber(payload): Observable<any> {
    console.log(payload);
    return this.httpClient.get<any>(
      `${this.baseURL}verifiy/phone?phoneNumber=%2b${payload.phoneNumber}&code=${payload.code}`
    );
  }

  verifyEmail(payload): Observable<any> {
    console.log(payload);
    return this.httpClient.get<any>(
      `${this.baseURL}verifiy/email?email=${payload.email}&code=${payload.code}`
    );
  }

  resendPhoneCode(payload): Observable<any> {
    return this.httpClient.post<any>(`${this.baseURL}verifiy/resend-phone`, payload);
  }

  resendEmailCode(payload): Observable<any> {
    return this.httpClient.post<any>(`${this.baseURL}verifiy/resend-email`, payload);
  }

  forgotPassword(payload): Observable<any> {
    console.log(payload);
    return this.httpClient.post<any>(`${this.baseURL}forgot-password`, payload);
  }

  setNewPassword(payload): Observable<any> {
    console.log(payload);
    return this.httpClient.post<any>(`${this.baseURL}set-new-password`, payload);
  }

  expirationCounter(timeout) {
    // this.tokenSubscription.unsubscribe();
    this.tokenSubscription = of(null)
      .pipe(delay(timeout))
      .subscribe((expired) => {
        this.logout();
        localStorage.removeItem('user');
        this.router.navigate(['/store/home']);
      });
  }
}
