import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../../../services/order/order.service';

@Component({
  selector: 'app-top-product-sales',
  templateUrl: './top-product-sales.component.html',
  styleUrls: ['./top-product-sales.component.scss'],
})
export class TopProductSalesComponent implements OnInit {
  constructor(private orderService: OrderService) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.topProducts.length,
    };
  }

  ngOnInit(): void {
    this.getTopProductSales();
  }
  p: number = 1;
  config: any;
  topProducts = [];
  getTopProductSales() {
    this.orderService.getTopProductSale(5).subscribe(
      (data) => {
        this.topProducts = data.result;
        console.log(this.topProducts);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  pageChanged(event) {
    this.config.currentPage = event;
  }
}
