import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AuthenticationService } from '../../../services/authentication/authentication.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
declare var $: any;
@Component({
  selector: 'app-otp-verification',
  templateUrl: './otp-verification.component.html',
  styleUrls: ['./otp-verification.component.scss'],
})
export class OtpVerificationComponent implements OnInit {
  @Input('phoneNumber')
  phoneNumber: any;

  @Input('email')
  email: any;

  @Input('emailVerified')
  emailVerified: any;

  @Input('phoneNumberVerified')
  phoneNumberVerified: any;

  @Output() close = new EventEmitter<any>();
  
  constructor(
    private authentication: AuthenticationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {}

  mobileOTP = [];
  emailOTP = []

  verifyMobile() {
    const mobileOTP = this.mobileOTP.join('');
    let number = JSON.stringify(this.phoneNumber);
    number = number.replace(/"|-| |\+/g, '');
    const payload = {
      phoneNumber: number,
      code: mobileOTP,
    };
    console.log(payload);
    this.spinner.show();
    this.authentication.verifyPhoneNumber(payload).subscribe(
      (data) => {
        this.mobileOTP = [];
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
        this.phoneNumberVerified = true;
      },
      (error) => {
        this.mobileOTP = [];
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  verifyEmail() {
    const emailOTP = this.emailOTP.join('');
    // let number = JSON.stringify(this.phoneNumber);
    // number = number.replace(/"|-| |\+/g, '');
    const payload = {
      email: this.email,
      code: emailOTP,
    };
    console.log(payload);
    this.spinner.show();
    this.authentication.verifyEmail(payload).subscribe(
      (data) => {
        this.emailOTP = [];
        this.emailVerified = true
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.emailOTP = [];
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  resendPhoneCode() {
    const payload = {
      phoneNumber: this.phoneNumber,
    };
    console.log(payload);
    this.spinner.show();
    this.authentication.resendPhoneCode(payload).subscribe(
      (data) => {
        this.toastr.success('OTP has been sent to your mobile', 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  resendEmailCode() {
    const payload = {
      email: this.email,
    };
    console.log(payload);
    this.spinner.show();
    this.authentication.resendEmailCode(payload).subscribe(
      (data) => {
        this.toastr.success('OTP has been sent to your email', 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
}
