import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../../../services/order/order.service';

@Component({
  selector: 'app-last-months-sales',
  templateUrl: './last-months-sales.component.html',
  styleUrls: ['./last-months-sales.component.scss'],
})
export class LastMonthsSalesComponent implements OnInit {
  constructor(private orderService: OrderService) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.lastMonthSales.length,
    };
  }

  ngOnInit(): void {
    this.getLastMonthSales();
  }
  p: number = 1;
  config: any;
  lastMonthSales = [];
  getLastMonthSales() {
    this.orderService.getLastMonthSales(6).subscribe(
      (data) => {
        this.lastMonthSales = data.result;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  pageChanged(event) {
    this.config.currentPage = event;
  }
}
