import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { CustomerService } from '../../../../services/Admin/customer/customer.service';
import { SocketService } from '../../../../services/socket/socket.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-edit-customer',
  templateUrl: './edit-customer.component.html',
  styleUrls: ['./edit-customer.component.scss'],
})
export class EditCustomerComponent implements OnInit {

  customer_Id: number;
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private customerService: CustomerService,
    private socketService: SocketService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
  ) {

    activatedRoute.queryParams.subscribe((params: Params) => {
      this.customer_Id = params['customer_Id'];
     
    });

    this.socketService.setupSocketConnection();
    this.getUsersSuggestion();
  }

  ngOnInit(): void {
    this.saveCustomerForm = new FormGroup({
      id: new FormControl(''),
      aa: new FormControl('', [Validators.required]),
    });

    if (this.customer_Id != undefined) {
      this.getUserById(this.customer_Id);
    }
  }

  saveCustomerForm: FormGroup;

  user: any = {};
  library: any;
  keyword = 'name';
  search = [];

  selectEvent(item) {
    console.log(item);
    this.getUserById(item.id);
  }

  onChangeSearch(search: string) {
    this.sendUserSearch(search);
  }
  onFocused(e) {
    // do something
  }

  sendUserSearch(keyword) {
    const obj = {
      keyword,
      type: ['customer', 'merchant'],
      limit: 10,
    };
    this.socketService.sendUserMessage(obj);
  }

  getUsersSuggestion() {
    this.socketService.getSuggestion().subscribe((data) => {
      console.log(data);
      this.library = data;
      this.search = data;
    });
  }

  getUserById(id) {
    this.customerService.getUserById(id).subscribe((data) => {
      console.log(data);
      this.user = data.body;
    });
  }

  formattedaddress = ' ';
  lat = '';
  lng = '';
  fulladdress = ' ';
  mapAddress;
  houseAddress = '';
  houseNumber;
  street;
  mapCity = '';
  mapState;
  mapcountry;
  postalCode;

  options = {
    componentRestrictions: {
      country: ['CA'],
    },
  };

  public AddressChange(address: any) {
    this.formattedaddress = address.formatted_address;
    console.log(address);

    this.lat = address.geometry.location.lat();
    this.lng = address.geometry.location.lng();
    this.houseAddress = address.name;
    this.fulladdress = address.formatted_address;
    for (let i = 1; i < address.address_components.length; i++) {
      this.mapAddress = address.address_components[i];
      if (this.mapAddress.long_name != '') {
        if (this.mapAddress.types[0] == 'street_number') {
          this.houseNumber = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'route') {
          this.street = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] === 'locality') {
          this.mapCity = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'administrative_area_level_1') {
          this.mapState = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'country') {
          this.mapcountry = this.mapAddress.long_name;
        } else if (this.mapAddress.types[0] == 'postal_code') {
          this.postalCode = this.mapAddress.long_name;
        }
        let streets = this.street && this.houseNumber ? this.houseNumber + ', ' + this.street : '';
        this.mapAddress = streets ? streets : this.street;
      }
    }
    console.log(this.lat);
    console.log(this.lng);
    console.log(address.name);
    console.log(this.street);
    console.log(this.mapCity);
    console.log(this.mapState);
    console.log(this.mapcountry);
    console.log(this.postalCode);
    this.user.address1 = address.name || '';
    this.user.address2 = this.street || '';
    this.user.city = this.mapCity || '';
    this.user.state = this.mapState || '';
    this.user.country = this.mapcountry || '';
    this.user.postalCode = this.postalCode || '';
    this.user.latitude = this.lat || '';
    this.user.longitude = this.lng || '';
  }



  onUpdateCustomer() {

    console.log("updateCustomer");
    console.log(this.user);

    var model = {
      "userId": this.user.id,
      "firstName": this.user.firstName,
      "middleName": this.user.middleName,
      "lastName": this.user.lastName,
      // "email": this.user.email,
      // "phoneNumber": this.user.phoneNumber,
      "address1": this.user.address1,
      "address2": this.user.address2,
      "city": this.user.city,
      "state": this.user.state,
      "country": this.user.country,
      "postalCode": this.user.postalCode,
      "status": this.user.status,
      "latitude": this.user.latitude,
      "longitude": this.user.longitude,
    }

    console.log(model);
    if (model != null) {
      this.spinner.show();
      this.customerService.updateCustomer(model)
        .subscribe(
          (data) => {
            console.log(data);
            this.spinner.hide();
            // this.toastr.success(data.message, 'Success');
          },
          (error) => {
            this.toastr.error(error.error.message, 'Error');
            this.spinner.hide();
          }
        );
    } else {

    }
  }


}
