  constructor(
    private router: Router,
    private shopService: ShopService,
    private productCatalogService: ProductCatalogService,
    private orderService: OrderService
  ) {

    this.shopService.selectedShopId.subscribe((data: any) => {
      if (data) {
        this.getLastDaysSales(data);
        this.getLastDaysStatusCount(data);
      }
    });
  }

  ngOnInit(): void {
    this.getAllProducts();
    this.getTopCategorySales();
    this.getTopProductSales();
    this.getLastMonthSales();

    // this.LineWaveChart3();
  }
  products = [];

  topCategorySales = [];
  topCategoryNames: any = [];
  topCategorySeries: any = [];

  topProducts = [];
  topProductSeries: any = [];
  topProductNames: any = [];

  lastMonthSales = [];
  monthlySalesMonths: any = [];
  monthlySalesSeries1: any = [];

  lastDaySales = [];
  lastDaysSalesSeries: any = [];
  averageSaleOfLastWeek: any = [];
  averageSaleOfSecondLastWeek: any = [];
  diffInSalesOfLastTwoWeeks: any = [];

  total = '2'
  notDelivered = '3'
  delivered = ''

  getAllProducts() {
    this.productCatalogService.getAllProducts(20, 1).subscribe(
      (data) => {
        console.log('P: ');
        console.log(data);
        this.products = data.body;
        console.log(this.products);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getTopCategorySales() {
    this.orderService.getTopCategorySale(6).subscribe(
      (data) => {
        console.log('CS: ');
        console.log(data.result);
        
        
        this.topCategorySales = data.result;
        this.topCategorySales.map((item) => {
          this.topCategoryNames.push(item.category_name);
          this.topCategorySeries.push(parseInt(item.sum, 10));
        });
        this.topCategoryNames = JSON.stringify(this.topCategoryNames);
        this.topCategorySeries = JSON.stringify(this.topCategorySeries);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getTopProductSales() {
    this.orderService.getTopProductSale(5).subscribe(
      (data) => {
        console.log('tp : ');
        
        this.topProducts = data.result;
        console.log(this.topProducts);
        this.topProducts.map((item) => {
          // this.topProductSeries = item.product_name;
          this.topProductSeries.push(item.sum);
          this.topProductNames.push(item.product_name);
        });
        this.topProductSeries = JSON.stringify(this.topProductSeries);
        this.topProductNames = JSON.stringify(this.topProductNames);
        console.log(this.topProductSeries);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getLastMonthSales() {
    this.orderService.getLastMonthSales(6).subscribe(
      (data) => {
        this.lastMonthSales = data.result;
        this.lastMonthSales.map((item) => {
          this.monthlySalesMonths.push(item.to_char);
          this.monthlySalesSeries1.push(item.sum);
        });
        this.monthlySalesMonths = JSON.stringify(this.monthlySalesMonths);
        this.monthlySalesSeries1 = JSON.stringify(this.monthlySalesSeries1);
        console.log(this.monthlySalesSeries1);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getLastDaysSales(shopId) {
    this.orderService.getLastDaysSales(shopId, 14).subscribe(
      (data) => {
        console.log(data)
        this.lastDaysSalesSeries = []
        this.lastDaySales = data.result.reverse()
        const currentWeek = this.lastDaySales.slice(0, 7).reverse()
        const lastWeek = this.lastDaySales.slice(7, 14).reverse()
        console.log(currentWeek)
        console.log(lastWeek)
        currentWeek.map(item => {
          console.log(item)
          this.lastDaysSalesSeries.push(parseInt(item.sum, 10))
          this.averageSaleOfLastWeek += item.sum
        })
        lastWeek.map(item => {
          this.averageSaleOfSecondLastWeek += item.sum
        })
        this.averageSaleOfLastWeek = this.averageSaleOfLastWeek / 7
        console.log(this.lastDaysSalesSeries)
        this.diffInSalesOfLastTwoWeeks = ((this.averageSaleOfLastWeek * 100) / (this.averageSaleOfLastWeek || 1))
        this.lastDaysSalesSeries = JSON.stringify(this.lastDaysSalesSeries);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  getLastDaysStatusCount(shopId) {
    this.orderService.getLastDaysStatus(shopId, 7).subscribe(
      (data) => {
        console.log(data)
        this.total = data.result.total;
        this.notDelivered = data.result.notdelivered;
        this.delivered = data.result.delivered;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  toTopCategorySales() {
    this.router.navigate(['merchant/top-category-sales']);
  }
  toTopProductSales() {
    this.router.navigate(['merchant/top-products-sales']);
  }
  toLastMonthsSales() {
    this.router.navigate(['merchant/last-months-sales']);
  }

  toAllProducts() {
    this.router.navigate(['merchant/total-products']);
  }

  toGrossSales(){
    this.router.navigate(['merchant/gross-sale'])
  }

  toTotalOrders(){
    this.router.navigate(['merchant/total-orders']);
  }

  toOutOfStock(){
    this.router.navigate(['merchant/out-of-stock']);
  }



