import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { CustomerService } from '../../../../../services/Admin/customer/customer.service';
import { SocketService } from '../../../../../services/socket/socket.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { jsPDF } from 'jspdf';

@Component({
  selector: 'app-view-log',
  templateUrl: './view-log.component.html',
  styleUrls: ['./view-log.component.scss'],
})
export class ViewLogComponent implements OnInit {

  customer_Id: number;
  customerLogs: any = [];
  customerName: string;

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private _customerService: CustomerService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private _elr: ElementRef,
    private socketService: SocketService
  ) {

    activatedRoute.queryParams.subscribe((params: Params) => {
      this.customer_Id = params['customer_Id'];
      this.customerName = params['customerName'] == undefined ? "" : params['customerName'] + "'s Logs";
    });

    this.socketService.setupSocketConnection();
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.customerData.length,
    };
    this.customerData = this.customerData.slice();
    this.getUsersSuggestion();
  }

  isActive: boolean = false;
  fieldHeader: any = [];
  _index: number = 0;
  _sort: string = 'ASC';

  ngOnInit(): void {
    this.getCustomers(this.config);
    this.getloadDummyData();
    if (this.customer_Id != undefined) {
      this.getCustomerLogs(this.customer_Id, this.config);
    }
  }

  customerDataDummy: any = [];
  customerData: any = [];
  fields: any = [
    { label: 'DATE', fieldName: 'createdAt' },
    { label: 'TIME', fieldName: 'time' },
    { label: 'IP/MAC ADDRESS', fieldName: 'ip' },
    { label: 'DETAILS', fieldName: 'message' },
  ];

  p: number = 1;
  config: any;
  pageSizes = [5, 10, 25, 50];
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };

  pageChanged(event) {
    this.config.currentPage = event;
    this.getCustomers(this.config);
    this.getCustomerLogs(this.customer_Id, this.config);
  }

  navigateToOrderDetail(id) {
    this.router.navigate([`store/dashboard/order-detail`, id]);
  }


  changePageSize() {
    this.getCustomers(this.config);
    this.getCustomerLogs(this.customer_Id, this.config);
  }

  library: any;
  keyword = 'name';
  search = [];

  selectEvent(item) {
    this.search = [item];
    const userIds = [];
    const firstName = [];
    this.search.map((item) => {
      userIds.push(item.id);
      firstName.push(item.firstName +" "+item.lastName);
    });
    this.config['userIds'] = userIds;
    console.log(this.config);
    this.getCustomers(this.config);
    this.customer_Id = userIds[0];
    this.customerName = firstName[0] + "'s Logs";
    this.getCustomerLogs(this.customer_Id, this.config);
  }

  onChangeSearch(search: string) {
    this.sendUserSearch(search);
  }

  onFocused(e) {
    // do something
  }

  // onEnter(e) {
  //   const userIds = [];
  //   const firstName = [];
  //   this.search.map((item) => {
  //     userIds.push(item.id);
  //     firstName.push(item.firstName);
  //   });
  //   this.config['userIds'] = userIds;
  //   console.log(this.config);
  //   this.getCustomers(this.config);
  //   this.customer_Id = userIds[0];
  //   this.customerName = firstName[0];
  //   this.getCustomerLogs(this.customer_Id,this.config);
  // }

  sortBy(orderBy, sort) {
    this.sort = {
      orderBy,
      sort,
    };
    this.getCustomers(this.config);
  }

  // //-------get view customer details-start-----
  getCustomers(config: any) {
    this.spinner.show();

    this._customerService.getUsers(config, this.sort).subscribe(
      (data) => {
        this.customerData = data.data;
        //to add in array active and inactive start--
        data.data.forEach((item) => {
          // item.actions = ["Active", "Inactive"];
          item.actions = [
            { name: 'Active', value: 'active' },
            { name: 'Inactive', value: 'inactive' },
          ];
        });

        this.config.totalItems = data.total;
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  // //-------get view customer details-end-------

  // //-------get view customer details-start-----
  getCustomerLogs(customer_Id, config: any) {
    this.spinner.show();

    this._customerService.getCustomerLogs(customer_Id, config, this.sort).subscribe(
      (data) => {
        console.log("data.data getCustomerLogs");
        console.log(data.data);
        this.customerLogs = data.data;
        //to add in array active and inactive start--
        this.config.totalItems = data.meta.total;
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  // //-------get view customer details-end-------

  sendUserSearch(keyword) {
    const obj = {
      keyword,
      type: ['customer', 'merchant'],
      limit: 10,
    };
    this.socketService.sendUserMessage(obj);
  }

  getUsersSuggestion() {
    this.socketService.getSuggestion().subscribe((data) => {
      console.log(data);
      this.library = data;
      this.search = data;
    });
  }

  //---------------------------------------------
  //---------------------------------------------

  exportColumns: any[];
  exportLst: ExportCustomer[];
  //--------export to pdf start-----------------

  exportPdf() {
    import('jspdf').then((jsPDF) => {
      import('jspdf-autotable').then((x) => {
        const doc = new jsPDF.default(0, 0);

        this.exportLst = [];

        if (this.customerData) {
          this.customerData.forEach((data) => {
            this.exportLst.push({
              FirstName: data.firstName,
              LastName: data.lastName,
              Email: data.email,
              Phone: data.phoneNumber,
              Address: data.address1,
              City: data.city,
              Date: data.createdAt,
            });
          });

          // this.driverService.exportDriversFilteredRecords(this.driverDetails).subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });

          doc.autoTable(this.exportColumns, this.exportLst);
          // });
        } else {
          // this.spinner.show();
          // this.driverService.exportAllDrivers().subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });
          //   this.spinner.hide();
          //   this.exportExcel(this.exportLst);
          // });
        }

        //doc.autoTable(this.exportColumns, this.lstDrivers);
        doc.save('CustomerTable.pdf');
      });
    });
  }
  //--------export to pdf end-----------------

  //----------export to excel----start-----------------------
  exportToExcel() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.customerData) {
      this.customerData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportExcel(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportExcel(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsExcelFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to excel----end-------------------------

  //---------------------------------------------
  //----------export to CSV----start-----------------------
  exportToCSV() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.customerData) {
      this.customerData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportCSV(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportCSV(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsCSVFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsCSVFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.csv';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to CSV----end-------------------------
  //---------------------------------------------

  //--------Dummy data-----start----------------
  getloadDummyData() {
    this.customerDataDummy = [
      {
        date: '17/05/2020',
        time: '05:24:00 AM',
        ipmacaddress: '192.168.0.12',
        details: 'Password Change',
      },
      {
        date: '17/05/2020',
        time: '05:24:00 AM',
        ipmacaddress: '192.168.0.12',
        details: 'Account Name Change',
      },
      {
        date: '17/05/2020',
        time: '05:24:00 AM',
        ipmacaddress: '192.168.0.12',
        details: 'Purchase Grocery',
      },
      {
        date: '17/05/2020',
        time: '05:24:00 AM',
        ipmacaddress: '192.168.0.12',
        details: 'Update card details',
      },
      {
        date: '17/05/2020',
        time: '05:24:00 AM',
        ipmacaddress: '192.168.0.12',
        details: 'Mobile verification',
      },
    ];
  }

  //--------Dummy data-----end------------------
}

export interface ExportCustomer {
  FirstName: string;
  LastName: string;
  Email: string;
  Phone: number;
  Address: string;
  City: number;
  Date: number;
}
