import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../../services/authentication/authentication.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
declare var $: any;

@Component({
  selector: 'app-dual-login',
  templateUrl: './dual-login.component.html',
  styleUrls: ['./dual-login.component.scss'],
})
export class DualLoginComponent implements OnInit {
  @Output() close = new EventEmitter<any>();
  merchantPasswordType: string = 'password';
  customerPasswordType: string = 'password';

  constructor(
    private router: Router,
    private authentication: AuthenticationService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {}

  customerEmail;
  customerPassword;
  merchantEmail;
  merchantPassword;
  verification;
  isVerifyOTP = false;
  ngOnInit(): void {
    // $('#phoneNumberVerificationModal').modal('show');
  }

  showPassword(type:any){
    switch (type) {
      case 'merchant':
        if (this.merchantPasswordType === 'password') {
          this.merchantPasswordType = 'text';
        } else {
          this.merchantPasswordType = 'password';
        }
        break;
      case 'customer':
        if (this.customerPasswordType === 'password') {
          this.customerPasswordType = 'text';
        } else {
          this.customerPasswordType = 'password';
        }
    
      default:
        break;
    }
    
  }

  signupPage(userType) {
    this.router.navigate([`signup/${userType}`]);
  }
  forgotPasswordPage(userType:string ) {
    localStorage.setItem('userTypeForResetPassword', userType);
    this.router.navigate(['forgot-password']);
  }

  loginCustomer() {
    const payload = {
      email: this.customerEmail,
      password: this.customerPassword,
      type: 'customer',
    };
    var message = '';
    if (!this.customerEmail && !this.customerPassword) {
       message =  'Please Enter Valid Credentials For Login';
       this.toastr.error(message, '');
       return;
    }
    else if (!this.customerEmail) {
        message = "Please Enter Email For Login"
      this.toastr.error(message, '');
      return;
    } else if (!this.customerPassword) {
      message = "Please Enter password For Login"
      this.toastr.error(message, '');
      return;
    } 
    this.spinner.show();
    this.authentication.login(payload).subscribe(
      (data) => {
        this.toastr.success(data.message, 'Success');
        localStorage.setItem('user', JSON.stringify(data));
        this.authentication.setLoginStatus(true);
        const redirectUrl = this.authentication.redirectUrl;
        if (data.type === 'merchant') {
          this.router.navigate([redirectUrl]);
        } else {
          this.router.navigate(['store/home']);
        }
        this.close.emit(false);
        this.spinner.hide();
      },
      (error) => {
        console.log(error.error.message);
        
        if(error.error.name === "Verification"){
          this.verification = error.error.message;
          console.log(this.verification);
          if(this.verification.status === 'inactive'){
            if(!this.verification.emailVerified || !this.verification.phoneNumberVerified){
              this.isVerifyOTP = true
            }else {
              this.toastr.error("Please Contact Admin", 'Error');
            }
         }
        }else {
          message = error.error.message;
          if (message.includes('Email does not exist')) {
            message = 'Email is not Exists'
          } else if(message.includes('password did not match')) {
            message = 'Please Enter Valid Credentials For Login'
          }
          this.toastr.error(message, '');
        }
        
        this.spinner.hide();
      }
    );
  }

  loginMerchant() {
    const payload = {
      email: this.merchantEmail,
      password: this.merchantPassword,
      type: 'merchant',
    };
    console.log(payload);
    this.spinner.show();
    this.authentication.login(payload).subscribe(
      (data) => {
        console.log(data);
        this.toastr.success(data.message, 'Success');
        this.authentication.setLoginStatus(true);
        localStorage.setItem('user', JSON.stringify(data));
        const redirectUrl = this.authentication.redirectUrl;
        if (data.type === 'merchant') {
          this.router.navigate([redirectUrl]);
        } else {
          this.router.navigate(['store/home']);
        }
        this.close.emit(false);
        this.spinner.hide();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  toggleOTPVerification(){
    this.isVerifyOTP = !this.isVerifyOTP;
  }
}
