import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../../services/authentication/authentication.service';
import { ProductLibraryService } from '../../../services/productLibrary/product-library.service';
import { ProductCatalogService } from '../../../services/productCatalog/product-catalog.service';
import { ShopService } from '../../../services/shop/shop.service';
import { SocketService } from '../../../services/socket/socket.service';
import jwt_decode from 'jwt-decode';
@Component({
  selector: 'app-store-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit {
  constructor(
    private authService: AuthenticationService,
    private router: Router,
    private shopService: ShopService,
    private productCatalogService: ProductCatalogService,
    private productLibraryService: ProductLibraryService,
    private socketService: SocketService
  ) {
    this.socketService.setupSocketConnection();
    this.authService.isLoggedIn.subscribe((data) => {
      this.isLoggedIn = data;
      console.log(this.isLoggedIn);
      if (this.isLoggedIn) {
        const type = JSON.parse(localStorage.getItem('user'))?.type;
        if (type === 'customer') {
          const token = JSON.parse(localStorage.getItem('user'))?.accessToken;
          if (token) {
            this.decoded = jwt_decode(token);
            const timenow = new Date().valueOf();
            const timeout = parseInt(this.decoded.exp, 10) - timenow / 1000;
            this.authService.expirationCounter(timeout * 1000);
          }
        } else {
          this.isLoggedIn = false;
        }
      }
    });
    this.getSuggestion();
    navigator.geolocation.getCurrentPosition((data) => {
      this.latitude = data.coords.latitude;
      this.longitude = data.coords.longitude;
      const obj = {
        latitude: data.coords.latitude,
        longitude: data.coords.longitude,
      };
      this.productCatalogService.setLocation(obj);
    });
  }

  ngOnInit(): void {}
  latitude;
  longitude;
  decoded: any;
  isLoggedIn: boolean = false;
  isDeliveryProcess: boolean = false;
  isLogin = false;
  searchBy = 'Products';
  toggleLogin() {
    this.isLogin = !this.isLogin;
  }
  toggleDeliveryProcess() {
    this.isDeliveryProcess = !this.isDeliveryProcess;
  }

  toProfile() {
    this.router.navigate(['store/profile']);
  }

  toHome() {
    this.router.navigate(['store/home']);
  }

  toAllCategory() {
    this.router.navigate(['store/all-category'], { state: { categoryId: 0 } });
  }

  toAccount() {
    this.router.navigate(['store/dashboard/account']);
  }
  toOrder() {
    this.router.navigate(['store/dashboard/orders']);
  }
  toTransaction() {
    this.router.navigate(['store/dashboard/transactions']);
  }

  goToContactUs() {
    this.router.navigate(['contactUs']);
  }

  setSearchBy(title) {
    this.searchBy = title;
  }

  logout() {
    this.authService.logout();
    localStorage.removeItem('user');
    this.router.navigate(['store/home']);
  }

  keyword = 'name';
  library = [];
  taxType = [];
  searchKeyword = '';

  selectEvent(item) {
    // do something with selected item
    // this.products[index].productLibraryId = item.product_id;
    // this.products[index].productName = item.product_name;
    console.log(item);
    this.searchKeyword = '';
    if (this.searchBy === 'Products') {
      console.log('Products');
      this.productCatalogService.setSearch(item);
      this.router.navigate(['store/search-products']);
    } else if (this.searchBy === 'Shop') {
      console.log('Shop');
      this.productCatalogService.setShop(item);
      // this.router.navigate(['store/shop-products']);
      this.router.navigate(['store/filter-products']);
    }
  }

  onChangeSearch(search: string) {
    // fetch remote data from here
    // And reassign the 'data' which is binded to 'data' property.
    console.log(search);
    this.searchKeyword = search;
    // this.sendSearch(search)
    if (this.searchBy === 'Products') {
      console.log('Products');
      this.sendProductSearch(search);
    } else if (this.searchBy === 'Shop') {
      console.log('Shop');
      this.sendShopSearch(search);
    }
  }
  onFocused(e) {
    // do something
  }

  onClear() {
    if (this.productCatalogService.search.getValue().name) {
      this.productCatalogService.setSearch({ name: '' });
      this.router.navigate(['store/filter-products']);
    }
  }

  onEnter() {
    if (this.searchKeyword) {
      console.log(this.searchKeyword);
      if (this.searchBy === 'Products') {
        this.productCatalogService.setSearch({ name: this.searchKeyword });
        // this.router.navigate(['store/search-products']);
        this.router.navigate(['store/filter-products']);
      }
    }
  }

  getProductList() {
    if (this.library.length < 10)
      this.productLibraryService.getProducts().subscribe((data) => {
        this.library = data.body.slice(0, 1000);
      });
  }

  sendProductSearch(keyword) {
    this.socketService.sendMessage(keyword);
  }

  sendShopSearch(keyword) {
    this.socketService.sendShopMessage(keyword);
  }

  getSuggestion() {
    this.socketService.getSuggestion().subscribe((data) => {
      console.log(data);
      this.library = data;
    });
  }

  public AddressChange(address: any) {
    const obj = {
      latitude: address.geometry.location.lat(),
      longitude: address.geometry.location.lng(),
      address: address.formatted_address,
    };
    console.log(obj);
    this.productCatalogService.setLocation(obj);
  }
}
