import {
  Component,
  OnInit,
  OnDestroy,
  AfterViewChecked,
  ElementRef,
  Output,
  EventEmitter,
  ViewChild,
} from '@angular/core';
import { ChatService } from '../../../services/chat/chat.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-chatbox',
  templateUrl: './chatbox.component.html',
  styleUrls: ['./chatbox.component.scss'],
})
export class ChatboxComponent implements OnInit, OnDestroy, AfterViewChecked {
  @ViewChild('scrollframe') scrollFrame: ElementRef;
  @Output() close = new EventEmitter<any>();

  constructor(private ChatService: ChatService, private router: Router) {
    this.ChatService.setupSocketConnection('customer', 'john doe');
    this.noAgentFoundListener();
    this.messageForCustomerListener();
  }

  ngOnInit(): void {
    this.ChatService.isCustomerOnline.subscribe((data) => {
      if (data.toString() === 'true') {
        this.customer = JSON.parse(localStorage.getItem('customer'));
        this.isCustomerOnline = true;
      } else {
        this.isCustomerOnline = false;
      }
    });

    this.router.events.subscribe((event) => {
      console.log('closing..............');
      this.ChatService.closeConnection();
    });
  }

  ngAfterViewChecked() {
    this.scrollToBottom();
  }

  ngOnDestroy(): void {
    console.log('destroying component');
    localStorage.removeItem('customer');
    this.ChatService.setCustomerStatus('false');
    this.ChatService.closeConnection();
  }
  // ngOnDestroy(): void {
  //   console.log('closing..............');
  //   this.ChatService.closeConnection();
  // }

  customer = {
    name: '',
    email: '',
    phone: '',
    message: '',
  };
  isCustomerOnline = false;

  isAgentOnline = true;

  messages = [];

  message = '';

  generateQuery() {
    if (this.customer.name && this.customer.message) {
      // Send an email to info@localgrocery.com
    }
  }

  startChat() {
    console.log(this.customer);
    if (this.customer.name) {
      localStorage.setItem('customer', JSON.stringify(this.customer));
      this.ChatService.setCustomerStatus('true');
      this.customer = JSON.parse(localStorage.getItem('customer'));
      const obj = {
        'user-name': this.customer.name,
        'user-type': 'customer',
        'user-email': this.customer.email,
        'user-phone': this.customer.phone,
      };
      this.ChatService.startChat(obj);
      // this.isCustomerOnline = true;
    }
  }

  sendMessage() {
    const obj = {
      message: this.message,
    };

    this.ChatService.sendMessageToAgent(obj);
    obj['userType'] = 'customer';
    obj['userName'] = 'Me';
    this.messages.push(obj);
    this.message = '';
  }

  noAgentFoundListener() {
    console.log('Observing no agent online');
    this.ChatService.getNoAgentFound().subscribe((data) => {
      console.log(data);
      if (data.toString() === 'true') {
        this.isAgentOnline = false;
        // this.ChatService.closeConnection();
      }
    });
  }

  messageForCustomerListener() {
    this.ChatService.receiveMessageForCustomer().subscribe((data) => {
      console.log(data);
      this.messages.push(data);
    });
  }

  scrollToBottom(): void {
    const el: HTMLDivElement = this.scrollFrame.nativeElement;
    el.scroll({
      top: el.scrollHeight,
    });
  }
}
