import { Component, NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './admin.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ViewCustomerComponent } from './customermanagement/view-customer/view-customer.component';
import { EditCustomerComponent } from './customermanagement/edit-customer/edit-customer.component';
import { CustomerLogsComponent } from './customermanagement/customer-logs/customer-logs.component';
import { ViewLogComponent } from './customermanagement/customer-logs/view-log/view-log.component';
import { ViewBusinessesComponent } from './storemanagement/view-businesses/view-businesses.component';
import { EditBusinessesComponent } from './storemanagement/edit-businesses/edit-businesses.component';
import { ViewStoreComponent } from './storemanagement/view-store/view-store.component';
import { BankingDetailsComponent } from './storemanagement/view-store/banking-details/banking-details.component';
import { StoreInformationComponent } from './storemanagement/view-store/store-information/store-information.component';
import { ManagerDetailsComponent } from './storemanagement/view-store/manager-details/manager-details.component';
import { AllUsersComponent } from './usermanagement/all-users/all-users.component';
import { CreateUserComponent } from './usermanagement/create-user/create-user.component';
import { AllRolesComponent } from './usermanagement/all-roles/all-roles.component';
import { PermissionDetailsComponent } from './usermanagement/all-roles/permission-details/permission-details.component';
import { CreateRolesComponent } from './usermanagement/create-roles/create-roles.component';
import { AllAccessPrivilidegesComponent } from './usermanagement/all-access-privilideges/all-access-privilideges.component';
import { NewAccessPrivilidegesComponent } from './usermanagement/new-access-privilideges/new-access-privilideges.component';
import { UserLogsComponent } from './usermanagement/user-logs/user-logs.component';
import { AllTicketsComponent } from './ticketmangement/all-tickets/all-tickets.component';
import { TicketDetailComponent } from './ticketmangement/ticket-detail/ticket-detail.component';
import { TicketUpdateComponent } from './ticketmangement/ticket-update/ticket-update.component';

const routes: Routes = [
  {
    path: '',
    component: AdminComponent,
    children: [
      {
        path: 'dashboard',
        component: DashboardComponent,
        pathMatch: 'full',
      },
      {
        path: 'view-customer',
        component: ViewCustomerComponent,
        pathMatch: 'full',
      },
      {
        path: 'edit-customer',
        component: EditCustomerComponent,
        pathMatch: 'full',
      },
      {
        path: 'customer-logs',
        component: CustomerLogsComponent,
        pathMatch: 'full',
      },
      {
        path: 'view-log',
        component: ViewLogComponent,
        pathMatch: 'full',
      },
      {
        path: 'view-businesses',
        component: ViewBusinessesComponent,
        pathMatch: 'full',
      },
      {
        path: 'edit-businesses',
        component: EditBusinessesComponent,
        pathMatch: 'full',
      },
      {
        path: 'view-store',
        component: ViewStoreComponent,
        pathMatch: 'full',
      },
      {
        path: 'store-information',
        component: StoreInformationComponent,
        pathMatch: 'full',
      },
      {
        path: 'banking-details',
        component: BankingDetailsComponent,
        pathMatch: 'full',
      },
      {
        path: 'manager-details',
        component: ManagerDetailsComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-users',
        component: AllUsersComponent,
        pathMatch: 'full',
      },
      {
        path: 'create-user',
        component: CreateUserComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-roles',
        component: AllRolesComponent,
        pathMatch: 'full',
      },
      {
        path: 'permission-details',
        component: PermissionDetailsComponent,
        pathMatch: 'full',
      },
      {
        path: 'create-roles',
        component: CreateRolesComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-access-privilideges',
        component: AllAccessPrivilidegesComponent,
        pathMatch: 'full',
      },
      {
        path: 'new-access-privilideges',
        component: NewAccessPrivilidegesComponent,
        pathMatch: 'full',
      },
      {
        path: 'user-logs',
        component: UserLogsComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-tickets',
        component: AllTicketsComponent,
        pathMatch: 'full',
      },
      {
        path: 'ticket-update',
        component: TicketUpdateComponent,
        pathMatch: 'full',
      },
      {
        path: 'ticket-detail/:ticketId',
        component: TicketDetailComponent,
        pathMatch: 'full',
      },
      {
        path: 'customer-support',
        loadChildren: () =>
          import('./chat-support/chat-support.module').then((module) => module.ChatSupportModule),
      },
      {
        path: 'inventory',
        loadChildren: () =>
          import('./inventory/inventory.module').then((module) => module.InventoryModule),
      },
      {
        path: 'order',
        loadChildren: () => import('./order/order.module').then((module) => module.OrderModule),
      },
      {
        path: 'promotion',
        loadChildren: () =>
          import('./promotion/promotion.module').then((module) => module.PromotionModule),
      },
      {
        path: 'library',
        loadChildren: () =>
          import('./library/library.module').then((module) => module.LibraryModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
