import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SignupComponent } from './public/signup/signup.component';
import { LoginComponent } from './public/login/login.component';
import { FogotPasswordComponent } from './public/fogot-password/fogot-password.component';
import { AdminComponent } from './panels/admin/admin.component';
import { MerchantComponent } from './panels/merchant/merchant.component';
import { AuthenticationGuard } from './guards/authentication/authentication.guard';
import { ContectUsComponent } from './panels/store/home/contect-us/contect-us.component';

const type = JSON.parse(localStorage.getItem('user'))?.type;
let redirectURL = 'store/home';
if (type === 'merchant') {
  redirectURL = 'merchant/dashboard';
} else if (type === 'customer') {
  redirectURL = 'store/home';
}
const routes: Routes = [
  // { path: '', redirectTo: 'merchant/dashboard', pathMatch: 'full' },
  { path: '', redirectTo: redirectURL, pathMatch: 'full' },
  {
    path: 'signup/:type',
    component: SignupComponent,
  },
  {
    path: 'login',
    component: LoginComponent,
  },
  {
    path: 'contactUs',
    component: ContectUsComponent,
  },
  {
    path: 'forgot-password',
    component: FogotPasswordComponent,
  },
  {
    path: '',
    children: [
      {
        path: 'admin',
        loadChildren: () =>
          import('./panels/admin/admin.module').then((module) => module.AdminModule),
      },
      {
        path: 'merchant',
        // component: MerchantComponent,
        loadChildren: () =>
          import('./panels/merchant/merchant.module').then((module) => module.MerchantModule),
        canActivate: [AuthenticationGuard],
      },
      {
        path: 'store',
        loadChildren: () =>
          import('./panels/store/store.module').then((module) => module.StoreModule),
      },
    ],
  },
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
