import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { UsersService } from '../../../../services/Admin/user/users.service';
import { CustomerService } from '../../../../services/Admin/customer/customer.service';
import { SocketService } from '../../../../services/socket/socket.service';
import { Router } from '@angular/router';
import { jsPDF } from 'jspdf';

@Component({
  selector: 'app-all-roles',
  templateUrl: './all-roles.component.html',
  styleUrls: ['./all-roles.component.scss'],
})
export class AllRolesComponent implements OnInit {

  isActive: boolean = false;
  _index: number = 0;
  _sort: string = 'ASC';

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private _usersService: UsersService,
    private _customerService: CustomerService,
    private socketService: SocketService,
    private router: Router,
    private _elr: ElementRef
  ) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.roleData.length,
    };
    this.roleData = this.roleData.slice();
    this.getUsersSuggestion();
  }

  ngOnInit(): void {
    this.getCustomers(this.config);
  }

  roleData: any = [];
  fields: any = [
    { label: 'ROLE NAME', fieldName: 'roleName' },
    { label: 'RIGHTS ASSIGN', fieldName: 'rightsAssign' },
    { label: 'DATE & TIME', fieldName: 'dateTime' },
  ];

  p: number = 1;
  config: any;
  pageSizes = [5, 10, 25, 50];
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };

  pageChanged(event) {
    this.config.currentPage = event;
    this.getCustomers(this.config);
  }

  navigateToOrderDetail(id) {
    this.router.navigate([`store/dashboard/order-detail`, id]);
  }

  changePageSize() {
    this.getCustomers(this.config);
  }

  library: any;
  keyword = 'name';
  search = [];

  selectEvent(item) {
    this.search = [item];
    // // do something with selected item
    // // this.products[index].productLibraryId = item.product_id;
    // // this.products[index].productName = item.product_name;
    // console.log(item)
    // if(this.searchBy === 'Products'){
    //   console.log('Products')
    //   this.productCatalogService.setSearch(item)
    //   this.router.navigate(['store/search-products']);
    // }
    // else if(this.searchBy === 'Shop'){
    //   console.log('Shop')
    //   this.productCatalogService.setShop(item)
    //   this.router.navigate(['store/shop-products']);
    // }
  }

  onChangeSearch(search: string) {
    this.sendUserSearch(search);
  }

  onFocused(e) {
    // do something
  }

  onEnter(e) {
    const userIds = [];
    this.search.map((item) => {
      userIds.push(item.id);
    });
    this.config['userIds'] = userIds;
    console.log(this.config);
    this.getCustomers(this.config);
  }

  sortBy(orderBy, sort) {
    this.sort = {
      orderBy,
      sort,
    };
    this.getCustomers(this.config);
  }

  // //-------get view customer details-start-----
  getCustomers(config: any) {
    this.spinner.show();

    this._usersService.getRoles(config, this.sort).subscribe(
      (data) => {
        this.roleData = data.body;
        //to add in array active and inactive start--
        data.body.forEach((item) => {
          // item.actions = ["Active", "Inactive"];
          item.actions = [
            { name: 'Active', value: 'active' },
            { name: 'Inactive', value: 'inactive' },
          ];
        });

        console.log(this.roleData);

        this.config.totalItems = data.total;
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  // //-------get view customer details-end-------

  sendUserSearch(keyword) {
    const obj = {
      keyword,
      type: ['customer', 'merchant'],
      limit: 10,
    };
    this.socketService.sendUserMessage(obj);
  }

  getUsersSuggestion() {
    this.socketService.getSuggestion().subscribe((data) => {
      console.log(data);
      this.library = data;
      this.search = data;
    });
  }

  onChangeCustomerStatus(userId, customerName, index) {

    if (index == 2) {
      this.router.navigate(['admin/view-log'], { queryParams: { customer_Id: userId, customerName: customerName } });
      return;
    } else if (index == 3) {
      this.router.navigate(['admin/edit-customer'], { queryParams: { customer_Id: userId } });
      return;
    }

    var status = index == 0 ? "active" : "inactive";
    var model = {
      "userId": userId,
      "status": status
    }

    console.log(model);
    if (model != null) {
      this.spinner.show();
      this._customerService.changeCustomerStatus(model)
        .subscribe(
          (data) => {
            console.log(data);
            this.spinner.hide();
            this.getCustomers(this.config);
            this.toastr.success(data.message, 'Success');
          },
          (error) => {
            this.toastr.error(error.error.message, 'Error');
            this.spinner.hide();
          }
        );
    } else {

    }
  }


  // //-------get view customer details-start-----
  getViewCustomerDetail(config: any) {
    this.spinner.show();

    this._customerService.getUsers(config).subscribe(
      (data) => {
        console.log('getViewCustomerDetail');
        console.log(data);
        this.roleData = data.data;
        //to add in array active and inactive start--
        data.data.forEach((item) => {
          // item.actions = ["Active", "Inactive"];
          item.actions = [
            { name: 'Active', value: 'active' },
            { name: 'Inactive', value: 'inactive' },
          ];
        });
        //to add in array active and inactive start--

        console.log('this.roleData ');
        console.log(this.roleData);

        this.config = {
          itemsPerPage: config.itemsPerPage,
          currentPage: config.currentPage,
          totalItems: data.total,
        };
        this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
  // //-------get view customer details-end-------

  //---------------------------------------------
  //---------------------------------------------

  exportColumns: any[];
  exportLst: ExportCustomer[];
  //--------export to pdf start-----------------

  exportPdf() {
    import('jspdf').then((jsPDF) => {
      import('jspdf-autotable').then((x) => {
        const doc = new jsPDF.default(0, 0);

        this.exportLst = [];

        if (this.roleData) {
          this.roleData.forEach((data) => {
            this.exportLst.push({
              FirstName: data.firstName,
              LastName: data.lastName,
              Email: data.email,
              Phone: data.phoneNumber,
              Address: data.address1,
              City: data.city,
              Date: data.createdAt,
            });
          });

          // this.driverService.exportDriversFilteredRecords(this.driverDetails).subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });

          doc.autoTable(this.exportColumns, this.exportLst);
          // });
        } else {
          // this.spinner.show();
          // this.driverService.exportAllDrivers().subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });
          //   this.spinner.hide();
          //   this.exportExcel(this.exportLst);
          // });
        }

        //doc.autoTable(this.exportColumns, this.lstDrivers);
        doc.save('CustomerTable.pdf');
      });
    });
  }
  //--------export to pdf end-----------------

  //----------export to excel----start-----------------------
  exportToExcel() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.roleData) {
      this.roleData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportExcel(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportExcel(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsExcelFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to excel----end-------------------------

  //---------------------------------------------
  //----------export to CSV----start-----------------------
  exportToCSV() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.roleData) {
      this.roleData.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportCSV(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportCSV(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsCSVFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsCSVFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.csv';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to CSV----end-------------------------
  //---------------------------------------------
}

export interface ExportCustomer {
  FirstName: string;
  LastName: string;
  Email: string;
  Phone: number;
  Address: string;
  City: number;
  Date: number;
}
