import { Component, OnInit, OnChanges, ViewChild, Input, SimpleChanges } from '@angular/core';
import {
  ApexAxisChartSeries,
  ApexChart,
  ChartComponent,
  ApexDataLabels,
  ApexXAxis,
  ApexPlotOptions,
  ApexFill,
} from 'ng-apexcharts';

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  dataLabels: ApexDataLabels;
  plotOptions: ApexPlotOptions;
  xaxis: ApexXAxis;
  colors: string[];
};

@Component({
  selector: 'app-avg-sales',
  templateUrl: './avg-sales.component.html',
  styleUrls: ['./avg-sales.component.scss'],
})
export class AvgSalesComponent implements OnInit, OnChanges {
  @ViewChild('chart') chart: ChartComponent;
  @Input() series: any;
  @Input() avgSales: any;
  @Input() diffInSales: any;
  public chartOptions: Partial<ChartOptions>;

  constructor() {
    this.chartOptions = {
      series: [
        {
          name: 'basic',
          data: [400, 430, 448, 690, 580, 470, 540],
        },
      ],
      chart: {
        type: 'bar',
        height: 250,
        toolbar: {
          show: false,
        }
      },
      plotOptions: {
        bar: {
          horizontal: false,
        },
      },
      dataLabels: {
        enabled: false,
      },
      xaxis: {
        categories: ['Mon', 'Tues', 'Wed', 'Thur', 'Fri', 'Sat', 'Sun'],
      },
      colors: ['rgb(242, 151, 0)'],
    };
  }
  
  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges) {
    console.log(changes.avgSales.currentValue)
    console.log(changes.series.currentValue)
    this.chartOptions.series[0].data = JSON.parse(changes.series.currentValue);
  }

}
