import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.baseURL;

  getUsers(config: any, sort: any = {}): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}users?pageNum=${config.currentPage}&pageSize=${config.itemsPerPage}${
        sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}${config.type ? '&type=' + config.type : ''}`,
      {
        headers,
      }
    );
  }

  getUserDetail(): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(`${this.baseURL}user`, {
      headers,
    });
  }

  updateUserDetail(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}user`, payload, {
      headers,
    });
  }

  changePassword(payload): Observable<any> {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.patch<any>(`${this.baseURL}changePassword`, payload, {
      headers,
    });
  }
}
