import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { PromotionService } from '../../../../services/promotion/promotion.service';
import { ShoppingCartService } from '../../../../services/shoppingCart/shopping-cart.service';
import { ToastrService } from 'ngx-toastr';
import * as moment from 'moment';
import { Router } from '@angular/router';
@Component({
  selector: 'app-promotion-slider',
  templateUrl: './promotion-slider.component.html',
  styleUrls: ['./promotion-slider.component.scss'],
})
export class PromotionSliderComponent implements OnInit {
  @Output() open = new EventEmitter<any>();
  constructor(
    private router: Router,
    private promotionService: PromotionService,
    private shoppingCart: ShoppingCartService,
    private toastr: ToastrService
  ) {
    this.getAllPromotions(this.sort);
  }

  ngOnInit(): void {}
  location;
  Arr = Array;
  products: any;
  rate = 5;
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };

  showDetail(index) {
    this.open.emit(this.products[index]);
  }

  getAllPromotions(sort) {
    this.promotionService.getAllProductsPromotion(10, 1, sort).subscribe(
      (data) => {
        console.log(data);
        this.products = data.body;
        this.products.map((item) => {
          const start = moment();
          const end = moment(item.enddate);
          console.log(start, end);
          const noOfDays = Math.ceil(moment.duration(end.diff(start)).asDays());
          item.daysLeft = noOfDays;
          item.image_link = item.image_link.replace('images/', '');
        });
        // this.products.map(item=>{
        //   console.log(item.promotionalPrice)
        //   item.promotionalprice = parseInt(item.promotionalprice, 10)
        //   item.actualprice = parseInt(item.actualprice, 10)
        // })
      },
      (error) => {
        // this.toastr.error(error.error.message, 'Error');
      }
    );
  }

  sortBy(field) {
    if (this.sort.orderBy != field) {
      this.sort.sort = this.sort.sort;
    } else {
      if (this.sort.sort === 'ASC') {
        this.sort.sort = 'DESC';
      } else {
        this.sort.sort = 'ASC';
      }
    }
    this.sort.orderBy = field;
    console.log(this.sort);
    this.getAllPromotions(this.sort);
  }

  addToCart(index) {
    if (this.products[index].stock > 0) {
      this.products[index].price = this.products[index].promotionalprice;
      this.products[index].quantity = 1;
      this.products[index].stock -= 1;
      this.products[index].type = 'single';
      this.shoppingCart.addProduct(this.products[index]);
    } else {
      this.toastr.error('This Product run out of stock', 'Error');
    }
  }

  left() {
    this.products.unshift(this.products.pop());
  }

  right() {
    this.products.push(this.products.shift());
  }

  navigateToAllPromotion() {
    this.router.navigate(['store/promotion-products']);
  }
}
