import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { io } from 'socket.io-client';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import jwt_decode from 'jwt-decode';
@Injectable({
  providedIn: 'root',
})
export class ChatService {
  socket;
  constructor(private httpClient: HttpClient) {}
  baseURL: any = environment.chatURL;
  isCustomerOnline = new BehaviorSubject<boolean>(false);
  setupSocketConnection(userType: any = '', userName: any = '', userId: any = '') {
    this.socket = io(this.baseURL, {
      query: {
        'user-type': userType,
        'user-name': userName,
        'user-id': userId,
      },
    });
  }

  newCustomer(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('newCustomerConnection', function (msg) {
        observer.next(msg);
      });
    });
  }

  customerDisconnected(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('customerDisconnected', function (msg) {
        observer.next(msg);
      });
    });
  }

  getNoAgentFound(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('noAgentOnline', function (msg) {
        observer.next(msg);
      });
    });
  }

  startChat(message) {
    this.socket.emit('startChat', message);
  }

  sendMessageToAgent(message) {
    this.socket.emit('messageToAgent', message);
  }

  receiveMessageForAgent(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('messageForAgent', function (msg) {
        observer.next(msg);
      });
    });
  }

  sendMessageToCustomer(message) {
    this.socket.emit('messageToCustomer', message);
  }

  receiveMessageForCustomer(): Observable<any> {
    return new Observable((observer) => {
      this.socket.on('messageForCustomer', function (msg) {
        observer.next(msg);
      });
    });
  }

  closeConnection() {
    this.socket.close();
  }

  setCustomerStatus(newValue): void {
    this.isCustomerOnline.next(newValue);
  }

  getChatSessions(pageNum = 1, pageSize = 10, sort) {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    const decoded: any = jwt_decode(token);
    const userId = decoded.id;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}chat/sessions/${userId}?pageNum=${pageNum}&pageSize=${pageSize}&${
        sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }

  getChatHistory(pageNum = 1, pageSize = 10, sort, sessionId) {
    const token = JSON.parse(localStorage.getItem('user')).accessToken;
    let headers = new HttpHeaders();
    headers = headers.set('Authorization', `Bearer ${token}`);
    return this.httpClient.get<any>(
      `${this.baseURL}chat/history/${sessionId}?pageNum=${pageNum}&pageSize=${pageSize}&${
        sort.orderBy ? '&orderBy=' + sort.orderBy : ''
      }${sort.sort ? '&sort=' + sort.sort : ''}`,
      {
        headers,
      }
    );
  }
}
