import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../../../services/order/order.service';

@Component({
  selector: 'app-top-category-sales',
  templateUrl: './top-category-sales.component.html',
  styleUrls: ['./top-category-sales.component.scss'],
})
export class TopCategorySalesComponent implements OnInit {
  constructor(private orderService: OrderService) {
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      totalItems: this.topCategories.length,
    };
  }

  ngOnInit(): void {
    this.getTopCategorySale();
  }
  p: number = 1;
  config: any;
  topCategories = [];
  getTopCategorySale() {
    this.orderService.getTopCategorySale(5).subscribe(
      (data) => {
        this.topCategories = data.result;
        console.log(this.topCategories);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  pageChanged(event) {
    this.config.currentPage = event;
  }
}
