import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BranchComponent } from './branch.component';
import { AddBranchComponent } from './add-branch/add-branch.component';
import { AddManagerComponent } from './add-manager/add-manager.component';
import { AllBranchesComponent } from './all-branches/all-branches.component';
import { BankingDetailsComponent } from './banking-details/banking-details.component';
const routes: Routes = [
  {
    path: '',
    component: BranchComponent,
    children: [
      { path: '', redirectTo: 'add-branch', pathMatch: 'full' },
      {
        path: 'add-branch',
        component: AddBranchComponent,
        pathMatch: 'full',
      },
      {
        path: 'add-manager',
        component: AddManagerComponent,
        pathMatch: 'full',
      },
      {
        path: 'all-branches',
        component: AllBranchesComponent,
        pathMatch: 'full',
      },
      {
        path: 'banking-details',
        component: BankingDetailsComponent,
        pathMatch: 'full',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class BranchRoutingModule {}
