import { Component, OnInit } from '@angular/core';
import { ProductCatalogService } from '../../../../services/productCatalog/product-catalog.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-out-of-stock',
  templateUrl: './out-of-stock.component.html',
  styleUrls: ['./out-of-stock.component.scss'],
})
export class OutOfStockComponent implements OnInit {
  constructor(
    private productCatalogService: ProductCatalogService,
    private spinner: NgxSpinnerService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.getAllProducts();
  }
  products = [];

  toggle(index) {
    this.products[index].action = !this.products[index].action;
    if (!this.products[index].action) {
      this.update(index);
    }
  }

  save(index) {
    console.log();
  }

  getAllProducts() {
    this.spinner.show();
    this.productCatalogService.getAllProducts(100, 1).subscribe(
      (data) => {
        this.products = data.body.data;
        console.log(this.products);
        // this.toastr.success(data.message, 'Success');
        this.spinner.hide();
        this.products.map((item, index) => {
          if (item.stock > 2) {
            this.products.splice(index, 1);
          }
        });
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  update(index) {
    const productId = this.products[index].id;
    const payload = {
      shopId: this.products[index].shopId,
      price: parseInt(this.products[index].price, 10),
      stock: this.products[index].stock,
    };
    console.log(payload);
    this.spinner.show();
    this.productCatalogService.updateProductByProductId(payload, productId).subscribe(
      (data) => {
        // this.toastr.success(data.message, 'Success');
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }
}
